.review-booking {
  height: calc(100% - 121px);
  display: flex;
  flex-direction: column;
  .booking-tabs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 8px;

    .navigation {
      .nav-link {
        padding-bottom: 4px 10px;
        font-size: 14px;
        &:hover {
          color: #7d63ec !important;
        }
        &.active {
          font-weight: 700;
          color: #7d63ec !important;
          border-bottom: 3px solid #7d63ec !important;
          &:focus,
          &:focus-visible {
            color: #7d63ec !important;
          }
        }
      }
      .nav-tabs {
        border-bottom: none !important;
      }
    }
    .header-separator {
      border-bottom: 0.5px solid #e7e4ff;
      flex-grow: 1;
    }
  }
  .review-billing-summary-container {
    height: calc(100% - 60px);
    overflow-y: auto;
    margin-top: 22px;
    .billing-summary-table {
      .slot-row {
        background-color: #f5f5f5;
        font-weight: bold;
      }

      .slot-date {
        font-size: 14px;
        font-weight: 700;
        line-height: 18px;
        color: #009896;
      }

      tbody tr.subtotal-label {
        :nth-child(1) {
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;
        }
        :last-child {
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
        }
        td {
          font-weight: bold;
          background: #ede7ff;
          border-bottom: 0.5px solid #e4e5eb;
        }
      }

      tbody tr.primary-background {
        :nth-child(1) {
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;
        }
        :last-child {
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
        }
        td {
          background: #7d63ec;
          color: white;
          padding: 10px 10px;
          font-weight: bold;
          font-size: 16px;
        }
      }
    }
  }
  .custom-nav-tabs {
    .nav-link.active {
      color: #745be3 !important;
      border-bottom: 3px solid #745be3 !important;
    }
  }

  .booking-details-container {
    height: calc(100% - 60px);
    overflow-y: auto;
   
    margin-top: 22px;
    .booking-details-accordion {
      .accordion-item {
        &.active {
          border: 0.1px solid #e4e5eb !important;
          box-shadow: 0px 4px 8px 0px #0a16290a !important;
          border-radius: 8px !important;
          .accordion-header {
            .accordion-button {
              background-color: #fff !important;
              border-radius: 8px !important;
            }
          }
        }
        .accordion-header {
          font-size: 16px;
          .accordion-button {
            background-color: #fbfafd !important;
            border-radius: 4px !important;
          }
        }

        border: none !important;
        border-radius: 4px !important;
        .booking-venue {
          .venue-info-label {
            font-size: 13px;
            font-weight: 400;
            color: #888888;
          }
          .venue-info-value {
            font-size: 14px;
            font-weight: 500;
            color: #282828;
          }
        }
      }

      .slot-data {
        .booking-info {
          .header {
            display: flex;
            justify-content: center;
            align-items: center;
            .heading {
              font-size: 16px;
              font-weight: 700;
              line-height: 24px;
              text-align: left;
              color: #5a557a;
            }
            .line-diff {
              border-top: 0.5px solid #e7e4ff;
              flex-grow: 1;
              margin-left: 8px;
            }
          }
          .header-text {
            font-size: 15px;
            font-weight: 600;
            line-height: 24px;
            text-align: left;
            color: #323232;
          }
        }
        //package-info
        .package-item {
          box-shadow: 0px 2px 6px 0px #0000000f;
          cursor: pointer;
          padding: 18px;
          border-radius: 8px;

          .label {
            font-size: 13px;
            font-weight: 400;
            line-height: 18px;
            color: #888888;
            margin-bottom: 6px;
          }

          .desc {
            font-size: 14px;
            font-weight: 500;
            line-height: 18px;
            color: #282828;
          }
        }
        .booking-data {
          .info-label {
            font-size: 13px;
            font-weight: 400;
            color: #888888;
          }
          .info-value {
            font-size: 14px;
            font-weight: 500;
            color: #282828;
          }
        }
        .venue-data {
          .info-label {
            font-size: 13px;
            font-weight: 400;
            color: #888888;
          }
          .info-value {
            font-size: 14px;
            font-weight: 500;
            color: #282828;
          }
        }
      }

      .amenities {
        padding: 14px 18px 18px 18px;
        border-radius: 8px 0px 0px 0px;
        background: #ffffff;
        gap: 8px;
      }
    }

    .accordion-button {
      &::after {
        margin-left: auto !important;
      }
    }

    .slot-date {
      display: flex;
      align-items: center;
      gap: 6px;
    }
  }
}

.review-booking-back-arrow {
  display: flex;
  align-items: center;
  margin-top: 12px;
  font-weight: 700;
  color: #433f5c;
  font-size: 18px;
}

.booking-success-modal {
  .booking-success {
    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
    text-align: left;
    color: #5a557a;
  }
}
