.documents-wrapper {
  height: 100%;

  .document-card-wrapper {
    .doc-top-wrapper {
      height: 200px;
      width: 100%;
      border-bottom: 1px solid #e1e3f6;
      padding: 15px 30px 0;
    }

    .doc-top-wrapper p {
      color: #556080;
      font-size: 13px;
    }
    .doc-bottom-wrapper {
      padding: 10px 25px;
      display: flex;
      justify-content: space-between;
    }
    .doc-bottom-wrapper .left-wrapper .text {
      font-size: 17px;
      font-weight: bold;
    }
    .doc-bottom-wrapper .left-wrapper .date-wrapper {
      font-size: 14px;
      color: #7b86ad;
    }
    .doc-bottom-wrapper .right-wrapper {
      margin-right: -10px;
    }
  }

  .add-doc-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 100%;
  }
  .add-doc-wrapper img {
    width: 130px;
  }
}

.create-new-modal {
  .new-doc-header {
    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
    text-align: left;
  }
  .modal-form {
    padding: 0px 24px 32px 20px !important;

    .modal-form-title {
      font-size: 20px !important ;
      font-weight: 600;
      line-height: 24px;
    }

    .document-type {
      font-size: 15px;
      font-weight: 500;
      line-height: 18px;
    }
  }

  .custom-btn {
    padding: 8px 19px 8px 19px !important;
    font-size: 15px !important;
    font-weight: 700;
  }
}
