.inquiry-info {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  .sticky-nav {
    position: sticky;
    top: 0;
    z-index: 90;
    background: #f7f7fa;
  }
}

.offcanvas-btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.inquiry-top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.inquiry-btns-wrapper {
  display: flex;
  align-items: center;
}

.read-only {
  opacity: 0.4;
}

.overflow-ellipse {
  text-overflow: ellipsis;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
}

.form-title-wrapper {
  display: flex;
  align-items: center;
  .title {
    margin: 0 10px 0 0;
    width: fit-content;
    white-space: nowrap;
  }
  .line {
    height: 1px;
    width: 100%;
    background-color: #bcb5f3;
  }
}

.add-new-wrapper-primary {
  display: flex;
  align-items: center;
  background: #f5f4ff;
  border-radius: 8px;
  height: 34px;
  font-size: 13px;
}
.slot-select-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slot-detail-wrapper {
  background-color: #f5f4ff;
  display: flex;
  align-items: center;
  font-size: 14px;
  padding: 7px 16px;
  border-radius: 8px;
  width: fit-content;
}
.slot-select-wrapper .form-select {
  background-color: #f5f4ff !important;
  width: fit-content !important;
  border: none;
}
.details-tab .nav-tabs {
  border: none;
}
.details-tab .nav-link {
  padding-bottom: 5px;
  font-size: 15px;
  border-bottom: 2px solid #ffffff !important;
  transition: 0.1s;
}
.details-tab .nav-item {
  display: flex;
  align-items: center;
  gap: 5px;
}
.notes-wrapper {
  position: relative;
  .shadow-above {
    box-shadow: 0px -5px 5px -5px #c9c9de80;
  }
}
.notes-container {
  overflow-y: auto;
  height: calc(100% - 124px);
}
.add-note-form-wrapper {
  height: calc(100% - 124px);
  overflow-x: hidden;
  textarea::placeholder,
  input::placeholder {
    font-size: 14px;
    font-weight: 500;
    color: #e4e5eb;
  }
  .description-data {
    text-align: justify;
  }
}
.notes-container::-webkit-scrollbar {
  display: none;
}
.note-wrapper {
  cursor: pointer;
  padding: 15px;
  border: 0.5px solid #e4e5eb;
  border-radius: 8px;
  margin-bottom: 15px;
}
.note-wrapper p {
  font-size: 13px;
  color: #888888;
  font-weight: 400;
}
.note-wrapper .edit {
  font-size: 10px;
  font-weight: 400;
  color: #888888;
}
.note-wrapper .title {
  font-size: 15px;
  color: #58596b;
}
.note-wrapper hr {
  // background-color: #e4e5eb;
  border: 1px solid #e4e5eb;
}
.note-wrapper .calender {
  font-size: 12px;
  color: #666666;
}
.note-badge {
  color: #ffffff;
  font-size: 10px;
  padding: 2px 12px;
  border-radius: 50px;
  background-color: #e5960a;
  text-align: center;
}
.venue-details-badge {
  color: #746e9a;
  font-size: 10px;
  font-weight: 500;
  padding: 2px 12px;
  border-radius: 50px;
  border: 1px solid #bcb5f3;
  text-align: center;
}
.notes-action-btns {
  display: flex;
  align-items: center;
  justify-content: end;
  /* position: absolute;
  bottom: 0; */
  width: 100%;
  gap: 30px;
}
.notes-action-btns .txt {
  font-size: 13px;
}
.grand-banquet-wrapper .accordion-button {
  display: flex;
  align-items: center;
  padding: 0 !important;
  padding-bottom: 15px !important;
}
.grand-banquet-wrapper .accordion-body {
  padding-top: 0 !important;
}
.grand-banquet-wrapper .accordion-button .title {
  color: #5a557a;
  font-weight: 600;
  white-space: nowrap;
  margin-right: 10px;
}
.grand-banquet-wrapper .accordion-button .line {
  width: 100%;
  height: 1px;
  background-color: #e7e4ff;
  margin-right: 10px;
}
.accordion-button::after {
  background-image: url(../../../../assets/img/down-arrow.png) !important;
  width: 10px !important;
  background-position: center;
  background-size: contain !important;
  margin-left: 10px !important;
}
.grand-banquet-wrapper .accordion-button:not(.collapsed)::after {
  background-image: url(../../../../assets/img/down-arrow.png) !important;
  width: 10px;
  background-position: center;
  background-size: contain;
  margin-left: 10px;
}
.design-wrapper img {
  width: 100px;
}
.inquiry-cancelled-error {
  padding: 15px 20px 2px;
  border-radius: 10px;
  border: 1px solid #eb5e5e;
  background-color: #fff2f2;
}
.waitlist-alert {
  font-size: 15px;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  border-radius: 10px;
  border: 1px solid #f2ae34;
  background-color: #fff7e8;
}
.inquiry-disposed {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 50px 0;
}
.inquiry-disposed img {
  width: 180px;
}
.bind-note-desc {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
}
.venue-slot-wrapper {
  display: flex;
  align-items: center;
  font-size: 14px;
}
.venue-slot-wrapper h6 {
  font-size: 14px;
}
.venue-banquet-wrapper {
  padding: 14px 16px;
  border: 1px solid #e4e5eb;
  border-radius: 7px;
}
.no-venue-added-error {
  display: flex;
  align-items: center;
  border: 1px solid #eb5e5e;
  border-radius: 7px;
  padding: 5px 10px;
  font-size: 13px;
}
.availability-wrapper {
  &.checkedVenue {
    border: 1px solid #61cf92;
  }
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #f5f4ff;
  border-radius: 10px;
  text-transform: capitalize;
  .fw-Bold {
    font-weight: bold !important;
  }
  .icon-label {
    color: #85868a;
    font-size: 14px;
  }
  .available-check:checked {
    background-image: url(../../../../assets/img/tick-white.png);
    background-size: auto;
    background-position: center;
  }
}
.not-available-wrapper {
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #f5f4ff;
  border-radius: 10px;
  text-transform: capitalize;
  .icon-label {
    color: #85868a;
    font-size: 14px;
  }
}

.form-label {
  &.checked {
    font-weight: bold;
    color: #1c1d21;
  }
}

.no-venue-selected {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 7px 18px;
  border-radius: 8px;
  border: 1px solid #e5960a;
  background: #fff6e6;
  .message {
    font-size: 15px;
    font-weight: 500;
  }
}

.date-time-text {
  font-size: 15px;
}
.custom-hr {
  color: #e4e5eb;
}
.custom-filter-accordion .accordion-button {
  padding: 13px 0 !important;
  color: #888888 !important;
  border-bottom: 1px solid #e4e5eb;
}
.dropdown-title {
  font-size: 15px;
  font-weight: 400;
  color: #323232;
  margin-left: 9px;
}
.reminder-form-wrapper {
  overflow-x: hidden;
  height: calc(100% - 124px);
  textarea::placeholder,
  input::placeholder {
    font-size: 14px;
    font-weight: 500;
    color: #e4e5eb;
  }
  .description-data {
    word-wrap: break-word;
    text-align: justify;
  }
}
.bind-reminder-desc {
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;
  word-wrap: break-word;
  text-align: justify;
}
.details-cards-wrapper {
  overflow-y: auto;
  height: calc(100% - 124px);
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.details-cards-wrapper::-webkit-scrollbar {
  display: none;
}

.guest-autocomplete-container {
  .e-ddl.e-input-group.e-control-wrapper .e-input {
    min-height: 0 !important;
    padding: 0px 0 4px !important;
    font-size: 1rem;
    font-family: "Lato", sans-serif;
  }

  .e-ddl.e-input-group input.e-input::selection {
    color: #fff !important;
    background: #745be3 !important;
  }

  .e-control-wrapper {
    border-color: #dee2e6 !important;
  }
}
.autocomplete-list-item {
  padding: 10px;
  .user-img {
    height: 36px;
    width: 36px;
    border-radius: 8px;
    background: rgb(245, 245, 250);
  }
  .user-data {
    height: 36px;
    .user-name {
      font-size: 12px;
      font-weight: 400;
      line-height: 14px;
      color: #888888;
      margin-top: 2px;
    }
    .user-phone {
      font-size: 15px;
      font-weight: 400;
      line-height: 18px;
      color: #745be3;
    }
  }
}

.selectedGuestSpinner {
  z-index: 500;
  position: relative;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;

  .state-header {
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
    color: #282828;
  }
  .state-info {
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    text-align: center;
    margin-top: 12px;
  }
  .state-button {
    padding: 8px 20px 8px 20px;
    margin-top: 24px;
    border-radius: 6px;
    border: 1px solid #7d63ec;
    font-size: 13px;
    font-weight: 700;
    line-height: 18px;
  }
}

.info-tab-wrapper {
  height: calc(100% - 91px);
}

.filter-btn {
  position: relative;

  .filter-number {
    position: absolute;
    top: -5px;
    right: -5px;
    background: #21c4c1;
    border-radius: 32px;
    width: 18px;
    height: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    color: white;
    font-weight: 700;
  }
}

.inquiry-table-heading {
  color: #5a557a;
  font-weight: bold;
  font-size: 16px;
}

.inquiry-details {
  .info-label {
    font-size: 13px;
    font-weight: 400;
    color: #888888;
  }
  .info-value {
    font-size: 14px;
    font-weight: 500;
    color: #282828;
  }
}

.filter-accordion {
  .accordion-button {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: #888888 !important;
    padding: 15px;
  }
  .accordion {
    .accordion-item {
      border: 0px !important;
      border-bottom: 1px solid#E4E5EB !important;
      .accordion-button:not(.collapsed) {
        background-color: transparent;
      }
    }
  }
}

.inquiry-filter {
  .e-date-wrapper {
    display: flex !important;
    flex-direction: row-reverse !important;
  }

  .e-input-group input.e-input,
  .e-input-group.e-control-wrapper input.e-input {
    text-align: left !important;
  }

  .e-input-group .e-clear-icon,
  .e-input-group.e-control-wrapper .e-clear-icon {
    position: absolute;
    top: 5px;
  }

  .e-control-wrapper.e-slider-container .e-slider .e-range {
    background: #745be3;
    height: 3px;
  }

  .e-control-wrapper.e-slider-container .e-slider .e-handle {
    background-color: #745be3;
    border-radius: 50%;
    border: 0;
  }
}

.status {
  padding: 2px 12px;
  gap: 8px;
  border-radius: 24px;
  font-size: 10px;
  font-weight: 500;
}

.status-bg-yellow {
  background: #e5960a0a;
}

.status-bg-success {
  background: #1cb1610a;
}

.status-bg-danger {
  background: #eb5e5e0a;
}

.status-bg-edited {
  padding: 4px 12px 4px 12px;
  border-radius: 24px;
  background: #5e81f40f;
  color: #5e81f4;
  text-align: center;
  font-size: 10px;
  font-weight: 500;
}

.notes-reminder-wrapper {
  background-color: #ffffff;
  border-radius: 7px;
  height: calc(100vh - 241px) !important;
  flex-shrink: 0 !important;

  .buttons-container {
    padding: 12px 24px 12px 24px;
    border-radius: 0px 0px 8px 8px;
    box-shadow: 0px 0px 10px 0px #c9c9de80;
    min-height: 60px;
    display: flex;
    justify-content: end;
  }
}

.quotation-container {
  height: 100%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.custom-time-select {
  border-color: #e4e5eb !important;

  &.invalid {
    border-color: #eb5e5e !important;
  }

  &:hover {
    background-color: transparent !important;
  }

  &.ant-picker-disabled {
    &:hover {
      background-color: #fafafa !important ;
    }
    background-color: #fafafa !important ;
  }
  &:focus-within {
    box-shadow: none;
  }
  .ant-picker-input > input {
    min-width: 90px !important;
    font-size: 15px !important;
  }
  &:hover {
    border-color: #e4e5eb !important;
  }

  .ant-picker-suffix {
    padding-right: 2px;
  }

  border-color: #dee2e6;
  border-left: 0px;
  border-right: 0px;
  border-top: 0px;
  border-radius: 0px;
  width: 100%;
  padding: 3px 7px 3px;
}
