.data-not-match {
  margin-top: 24px;
  height: calc(100% - 24px);
  display: flex;
  justify-content: center;
  align-items: center;

  h2 {
    font-size: 24px;
    font-weight: 700;
    line-height: 30px;
    text-align: center;
    color: #323232;
  }

  .description {
    font-family: Lato;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
    color: #888888;
  }

  img {
    width: 181px;
    height: 200px;
  }
}
