.subscription-details-wrapper {
  .sub-card {
    box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px;
    border-radius: 10px;
    border: none !important;
    .plan {
      font-family: Lato;
      font-size: 18px;
      font-weight: 500;
      line-height: 30px;
      color: #999b9f;
    }
    .plan-wrapper {
      padding: 18px 0 16px 30px;
      display: flex;
      flex-direction: column;
    }

    .plan-heading-text {
      font-family: Lato;
      font-size: 22px;
      font-weight: 700;
      line-height: 30px;
    }

    .user-wrapper {
      padding: 18px 0 16px 30px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    .progress-container {
      width: 70px;
      height: 70px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .inactive-plan-warning {
    padding: 16px 24px 20px 24px;
    border-radius: 5px;
    background: #FFFFFF;

    .inactive-plan-heading {
      font-size: 22px;
      font-weight: 700;
      line-height: 30px;
      color: #eb5e5e !important;
    }

    .inactive-plan-desc {
      padding: 12px 18px 12px 18px;
      border-radius: 8px;
      border: 1px;
      border: 1px solid #eb5e5e;
      background: #fff2f2;
      font-size: 15px;
      color: #323232;
    }
  }
}
