.InputWithIcon {
  position: relative;
  &.start {
    .icon {
      left: 0;
    }
    .input-div {
      .form-control {
        padding-left: 25px !important;
      }
    }
    &.hidden-icon {
      .input-div {
        .form-control {
          padding-left: 4px !important;
        }
      }
    }
  }
  &.end {
    .icon {
      right: 0;
    }
    .input-div {
      .form-control {
        padding-right: 25px !important;
      }
    }
  }
  .icon {
    position: absolute;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 4px;
    top: 0;
    bottom: 0;
  }
}
