.user-management {
  height: calc(100% - 156px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.subscriptions {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.help-support {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

//user-management-header

.header {
  min-height: 72px;
  display: flex;
  align-items: center;
  transition: 0.3s;
  font-size: 16px;
  justify-content: space-between;
}
.heading-title {
  font-size: 20px;
  color: #433f5c;
  font-weight: 600;
}
.user-title {
  font-size: 20px;
  font-weight: 500;
  line-height: 30px;
  text-align: left;
  color: #745be3;
}
.users-profile {
  color: #7d63ec;
  font-size: 18px;
  font-weight: 600;
}

.image-container {
  height: 170px;
  width: 170px;
}
.profile-image {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.profile-uploader {
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.profile-upload {
  position: absolute;
}
.design-desc {
  color: #7d63ec;
  padding: 6px 14px;
  background-color: #7c63ec2a;
  width: fit-content;
  font-size: 14px;
  border-radius: 25px;
}
.upload-img {
  position: relative;
  bottom: 40px;
  right: 0;
  left: 110px;
}

.canvasbtn {
  background-color: #7d63ec !important;
  border-color: #7d63ec !important;
  padding: 8px 20px !important;
}
.canvasbtn-toggle {
  background-color: #7d63ec !important;
  border-color: #7d63ec !important;
  padding: 8px 14px !important;
}
.btn-group > .btn-group:not(:first-child) > .btn,
.btn-group > .btn:nth-child(n + 3),
.btn-group > :not(.btn-check) + .btn {
  border-left: 1px solid #fff !important;
}
.btn-group-vertical > .btn-check:checked + .btn,
.btn-group-vertical > .btn-check:focus + .btn,
.btn-group-vertical > .btn.active,
.btn-group-vertical > .btn:active,
.btn-group-vertical > .btn:focus,
.btn-group-vertical > .btn:hover,
.btn-group > .btn-check:checked + .btn,
.btn-group > .btn-check:focus + .btn,
.btn-group > .btn.active,
.btn-group > .btn:active,
.btn-group > .btn:focus,
.btn-group > .btn:hover {
  border-right: 1px solid #fff !important;
}
.dropdown-item.active,
.dropdown-item:active {
  color: #212529 !important;
  text-decoration: none;
  background-color: #f1f6f9 !important;
}

.header-container {
  padding: 16px 0px;
}

.header-info {
  font-size: 20px;
  font-weight: 500;
  color: #745be3;
}

.access-icon span {
  cursor: pointer;
}

.verification-link {
  font-size: 15px;
  font-weight: 700;
  line-height: 20px;
  color: #5e81f4;
  margin-left: 5px;
  cursor: pointer;
}
.account-primary-badge {
  padding: 0px 12px 0px 12px;
  border-radius: 24px;
  background: #009896;
  color: #fff;
  font-size: 12px;
}

.access-table{
  td{
    vertical-align: middle !important;
  }

  tbody tr  {
    color: #525361;
    font-weight: 500;
    font-size: 15px;
    border-bottom: 1px solid #e4e5eb;
  }
  
}

.hover-row:hover {
  background-color: #f5f5f5 !important; /* Light gray background */
}