.fb-items {
  display: flex;
  height: 100%;
  flex-direction: column;
  .delete-col {
    width: 70px;
  }
  .nav-tabs.custom-nav-tabs {
    border: none !important;
  }

  .custom-tab-content-data {
    // food Package css
    .food-item-width {
      width: 200px;
    }
    .food-type-width {
      width: 150px;
    }
    .item-cost {
      width: 130px;
      &.invalid {
        border-color: #eb5e5e;
      }
    }
    // food Package css
    .beverage-liquor-package-item-width {
      width: 200px;
    }

    .item-cost {
      width: 130px;
      &.invalid {
        border-color: #eb5e5e;
      }
    }
  }
}
