.card-info-wrapper {
  margin-bottom: 20px;
  .card-info-title {
    font-size: 15px;
    color: #666666;
  }
}

.custom-tab-content {
  border-radius: 7px;
  margin-top: 20px;
  width: 100%;
}

.top-wrapper {
  display: flex;
  justify-content: space-between;
}

.venue-detail-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* margin-top: 10%; */
  min-height: 100%;
  width: 100%;
}
.venue-detail-wrapper img {
  width: 130px;
}
.add-btn {
  display: flex !important;
  gap: 4px;
  align-items: center;
}
.venue-details-progress-wrapper {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding: 20px 20px;
  border-radius: 7px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  height: 100%;
}
.progress-bar-item {
  display: flex;
  align-items: center;
  gap: 9px;
  cursor: pointer;
}
.progress-item-title-active {
  color: var(--primary-color);
  font-weight: 600;
  font-size: 15px;
}
.progress-item-title-pending {
  color: var(--text-color-inactive);
  font-size: 15px;
}
.progress-item-title-done {
  font-size: 15px;
}
.progress-vertical-line {
  height: 30px;
  width: 1px;
  border: 1px dotted #d8ccff;
  margin-left: 12px;
}

.account-details-wrapper {
  min-height: 100%;
  /* padding-bottom: 50px; */
}
.custom-tab-content .tab-pane {
  height: 100%;
}
.download-template-btn span {
  font-size: 15px;
}

.title-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title-wrapper h4 {
  font-size: 20px;
  font-weight: 600;
}
.middle-wrapper .title {
  font-size: 16px;
  font-weight: 600;
}
.package-item-name-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
  background-color: #f9f9ff;
  font-weight: 600;
  border-bottom: 1px solid #d4cfff;
  border-radius: 5px;
  padding: 8px;
}
.package-item-name-body {
  padding: 0 5px;
}
.item-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #dee2e6;
}
.item-wrapper .name {
  color: #5d5e62;
  font-size: 16px;
}
.count-wrapper {
  border: 1px solid #e4e5eb;
  border-radius: 5px;
  display: flex;
  gap: 8px;
  padding: 0 5px;
}
.count-wrapper .icon {
  cursor: pointer;
}
.custom-accordion .accordion-item {
  border: none;
}
.custom-accordion .accordion-button {
  background-color: white;
  padding: 0;
  padding-bottom: 6px;
}
.custom-accordion .accordion-button:focus {
  box-shadow: none;
}
.custom-accordion .accordion-button:not(.collapsed) {
  background: none;
}
.custom-accordion .accordion-body {
  padding: 0;
  padding-top: 0px;
}
.selected-count-wrapper {
  color: #8181a5 !important;
}
.amenity-wrapper {
  border: 1px solid #e9ecee;
  padding: 10px;
  width: 100%;
  border-radius: 7px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.amenity-wrapper span {
  display: inline-block;
  max-width: 150px; /* Adjust the width as needed */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  vertical-align: middle;
}
.add-amenity-wrapper {
  display: flex;
  align-items: center;
  padding: 3px 10px;
  border: 1px solid #e9ecee;
  border-radius: 7px;
  cursor: pointer;
}
.add-amenity-wrapper input {
  border: none;
}
.add-amenity-wrapper input:focus {
  box-shadow: none !important;
}
.add-amenity-wrapper input::placeholder {
  font-size: 16px !important;
}
.uncheked-amenties {
  background: #e9ecee;
  width: 22px;
  height: 22px;
  padding: 2px;
  border-radius: 4px;
}
.amenities-over {
  border: 1px solid #5e81f4;
}
.add-amenity-input::placeholder {
  font-size: 14px;
  font-weight: 700;
  color: #2e3241 !important;
}
.bind-text {
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 170px;
  white-space: nowrap;
}
.edit-input-wrapper input {
  border: none !important;
  width: 100%;
}
.edit-input-wrapper input:focus {
  /* border-bottom: 1px solid lightgrey !important; */
  box-shadow: none !important;
  outline: none;
}
.img-wrapper img {
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px;
  border-radius: 10px;
}
/* Documents */
.doc-content {
  height: calc(100% - 45px);
  overflow-y: auto;
  /* font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  color: #556080 !important; */
}

.document-text {
  font-size: 18px;
  font-weight: 400;
  line-height: 32px;
  color: #556080 !important;
}
.document-card-wrapper {
  border: 1px solid #e1e3f6;
  /* max-height: 300px; */
}
.bind-text-nav {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  font-weight: 400;
  line-height: 20px;
  text-align: justify;
  color: #556080;
}
.doc-top-wrapper {
  width: 100%;
  border-bottom: 1px solid #e1e3f6;
  padding: 15px 30px 0;
}

.doc-top-wrapper p {
  color: #556080;
  font-size: 13px;
}
.doc-bottom-wrapper {
  padding: 10px 25px;
  display: flex;
  justify-content: space-between;
}
.doc-bottom-wrapper .left-wrapper .text {
  font-size: 17px;
  font-weight: bold;
}
.doc-bottom-wrapper .left-wrapper .date-wrapper {
  font-size: 14px;
  color: #7b86ad;
}
.doc-bottom-wrapper .right-wrapper {
  margin-right: -10px;
}
.right-wrapper .dropdown-toggle::after {
  display: none;
}
.right-wrapper .dropdown-toggle.show,
.right-wrapper .dropdown-toggle:focus {
  border: none !important;
}

.doc-upload-wrapper {
  border: 1px solid #e4e5eb;
  border-radius: 16px;
  padding: 24px;
  position: relative;
}
.doc-upload-wrapper .title {
  padding: 0 8px;
  position: absolute;
  top: -12px;
  background-color: white;
  width: fit-content;
  font-size: 14px;
  left: 16px;
}
.cost-input-field {
  /* border: 1px solid #ced4da; */
  border: 1px solid #e5e5e5;
  border-radius: 6px;
  display: flex;
  align-items: center;
  padding: 0 10px;
  background-color: white;
}

.cost-input-field.invalid {
  border-color: #eb5e5e !important;
}

.cost-input-field input {
  border: none;
}
.cost-input-field input:focus {
  box-shadow: none;
}
.cost-input-field input:disabled {
  box-shadow: none;
  background-color: white;
}

.no-count {
  padding: 0 28px;
}

.middle-wrapper {
  border: 1px solid #e4e5eb;
  border-radius: 10px;
  padding: 15px;
}
