.inquiry-status {
  .inquiry-badge {
    color: white;
    font-size: 13px;
    padding: 2px 12px;
    border-radius: 24px;
  }
  .active {
    background-color: #32cb66;
  }
  .cancelled {
    background-color: #eb5e5e;
  }
  .disposed {
    background-color: #eb5e5e;
  }
  .completed {
    background-color: #7d63ec;
  }
}
