.dashboard-container {
  height: calc(100% - 91px);
  width: 100%;
  overflow-y: auto;

  .card-header-text {
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    color: #1c1d21;
    text-wrap: nowrap;
  }

  .dashboard-card-container {
    row-gap: 24px;
    .card-min-height {
      min-height: 132px;
    }
    .data-card {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 30px;
      background: #fff;
      border-radius: 12px;
      height: 132px;
      box-shadow: 0px 4px 16px 0px #0000000a;

      .data {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 12px;
        .card-title {
          color: #848484;
          font-family: Lato;
          font-size: 18px;
          font-style: normal;
          font-weight: 500;
          line-height: 26px;
          color: #848484;
          text-wrap: nowrap;
        }
        .card-value {
          color: #1c1d21;
          text-align: right;
          font-size: 24px;
          font-weight: 700;
          line-height: 32px;
        }
      }

      .card-right {
        display: flex;
        flex-direction: column;
        gap: 8px;
        .guest-percent {
          font-size: 18px;
          font-weight: 600;
          line-height: 28px;
          text-align: right;
          .increase {
            color: #1cb161;
          }
          .decrease {
            color: #e4251f;
          }
        }
        .guest-label {
          font-size: 16px;
          font-weight: 400;
          line-height: 27px;
          color: #848484;
        }
        .guest-value {
          font-size: 16px;
          font-weight: 500;
          line-height: 27px;
          color: #848484;
        }
      }
      .card-graph {
        height: 132px;
        width: 132px;
      }
    }
    .data-card-graph {
      padding: 30px 10px 30px 30px !important;
    }

    .scale-graph-container {
      width: 100px;
      height: 64px;
    }
  }

  .graph-data {
    row-gap: 24px;
    .booking-distribution {
      border-radius: 12px;
      background: #fff;
      padding: 17px 20px 0 20px;
      height: 382px;
      .legend {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 18px;
        font-family: Lato;
        font-size: 12px;
        font-weight: 400;
        line-height: 21px;
        text-align: left;
        margin-top: 14px;
        .legend-icon {
          display: inline-flex;
          justify-content: center;
          align-items: center;
        }
        .opacity {
          opacity: 0.2;
        }
      }
    }

    .occupancy-details {
      border-radius: 12px;
      background: #fff;
      padding: 17px 20px;
      height: 380px;
      .graph-container {
        height: 246px;
      }
      .custom-legend-occupancy {
        display: flex;
        column-gap: 40px;
        margin-top: 14px;
        .opacity {
          opacity: 0.2;
        }

        .legend-item {
          display: flex;
          align-items: center;

          .icon-box {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: flex-start;
          }
          .legend-icon {
            width: 15px;
            height: 15px;
            background: #fff;
            margin-top: -4px;
          }

          .purple {
            border: 4px solid #6a4ad4;
          }

          .green {
            border: 4px solid #4ad4ab;
          }

          .legend-text {
            font-family: Lato;
            font-size: 12px;
            font-weight: 400;
            line-height: 21px;
            text-align: left;
          }

          .legend-details {
            font-family: Lato;
            font-size: 10px;
            font-weight: 700;
            line-height: 14px;
            color: #444444;
          }

          .occupancy-percent-text {
            font-family: Lato;
            font-size: 10px;
            font-weight: 300;
            line-height: 14px;
            color: #888888;
          }
        }
      }
    }

    .revenue-details {
      border-radius: 12px;
      background: #fff;
      padding: 17px 20px;
      height: 382px;
    }

    .upcoming-bookings {
      border-radius: 12px;
      background: #fff;
      padding: 20px;
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      height: 784px;
      .booking-list {
        overflow-y: auto;
        scrollbar-width: none;
        .booking-item {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          gap: 16px;
          border-bottom: 0.5px solid #8181a5;
          padding-bottom: 20px;
          padding-top: 20px;
          .list-header {
            width: 100%;
            .header-text {
              display: flex;
              justify-content: space-between;
              align-items: center;
              align-self: stretch;
              .id {
                color: #1c1d21;
                font-size: 13px;
                font-weight: 700;
                line-height: 18px;
              }
            }
            .description {
              color: #1c1d21;
              font-size: 13px;
              font-weight: 500;
              line-height: 18px;
            }
          }
          .date-text {
            color: #888;
            font-size: 12px;
            font-weight: 500;
            line-height: 16px;
          }
        }
        .no-bookings {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          gap: 26px;
        }
      }
    }
  }

  .user-performance {
    min-height: 415px;
    max-height: 415px;

    .performance-table {
      height: 100%;

      .table-container {
        height: 322px;
        overflow-y: auto;
        background: #fff;
        .simple-table {
          thead th {
            position: sticky !important;
            top: 0;
          }
          tbody tr td {
            vertical-align: middle;
          }
          tr:hover td {
            background-color: #eeeeee !important;
          }
          .custom-header {
            color: #58596b;
          }
        }
      }

      border-radius: 12px;
      background: #fff;
      padding: 20px;

      h5 {
        color: #1c1d21;
        font-size: 16px;
        font-weight: 700;
        line-height: 24px;
      }
    }
    .user-avatar {
      width: 26px;
      height: 26px;
      border-radius: 50%;
      margin-right: 10px;
    }
    .user-name {
      display: flex;
      align-items: center;
      font-family: Lato;
      font-size: 14px;
      font-weight: 500;
      line-height: 18px;
      text-align: left;
      color: #323232;
    }
    .table-values {
      font-family: Lato;
      font-size: 15px;
      font-weight: 500;
      line-height: 18px;
      color: #323232;
    }
  }

  .source-distribution {
    min-height: 340px;
    max-height: 340px;
    border-radius: 12px;
    background: #fff;
    padding: 20px;

    .custom-legend-source {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      height: 100%;
      gap: 20px;
      margin-left: 50px;

      .legend-icon {
        width: 15px;
        height: 15px;
        background: #fff;
        border-radius: 100%;
        // margin-top: 7px;
      }

      .total {
        font-family: Lato;
        font-size: 26px;
        font-weight: 700;
        line-height: 32px;
        text-align: left;
        color: #1c1d21;
      }

      .item {
        display: flex;
        flex-direction: row;
        gap: 8px;

        .legend-text {
          font-family: Lato;
          font-size: 16px;
          font-weight: 500;
          line-height: 27px;
          text-align: left;
          color: #8e909e;
        }
      }

      .purple {
        border: 4px solid #6a4ad4;
      }

      .yellow {
        border: 4px solid #f6d69c;
      }
    }
  }

  .dashboard-dropdown-wrapper {
    display: flex;
    padding: 1px 16px;
    align-items: center;
    border-radius: 24px;
    border: 1px solid #e4e5eb;
    height: 30px;
    .e-input-group {
      border-bottom: 0 !important;
      margin-bottom: 0px !important;
      width: 152px !important;
    }
  }

  .year-list {
    width: 114px;
  }
}

.donut-tooltip {
  width: 148.38px;
  border-radius: 3px;
  padding: 8px;
  background-color: #ffffff;
  position: relative;
  .tooltip-header {
    display: flex;
    justify-content: space-between;
    .header-label {
      font-size: 12px;
      font-weight: 600;
      line-height: 14px;
      color: #585858;
    }
    .header-value {
      font-size: 12px;
      font-weight: 500;
      line-height: 14px;
      color: #444444;
    }
  }
  .status-item {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;
    .status-value .status-label {
      font-size: 12px;
      font-weight: 500;
      line-height: 14px;
      color: #848484;
    }
  }
  .divider {
    border: 0.5px solid #dbdbdb;
  }
