  //   border-width: 15px;
  //   border-style: solid;
  //   border-color:  transparent #ffffff transparent  transparent;
  // }
}

.revenue-tooltip {
  width: 120px;
  height: 29px;
  background-color: #fff;
  color: #444444;
  text-align: center;
  border-radius: 6px;
  padding: 8px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  box-shadow: 0px 4px 12px 0px #0000001f;
  z-index: 10000;

  .label {
    font-size: 12px;
    font-weight: 600;
    line-height: 14px;
    text-align: left;
    color: #666666;
  }

  &::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #fff transparent transparent transparent;
  }
}

.graph-no-data {
  padding: 25px 34px;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffffcc;
  .content {
    display: flex;
    gap: 16px;
    justify-content: center;
    img {
      width: 70px;
      height: 70px;
    }
    .content-title {
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      color: #323232;
    }
    .content-desc {
      font-size: 12px;
      font-weight: 400;
      line-height: 26px;
      color: #abb2b3;
    }
  }
}

.dashboard-loader {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.graph-daterange-wrapper {
  width: 213px;
  margin-left: 12px;
  height: 30px;
  .ant-picker {
    width: 100%;
    height: 30px !important;
    color: #323232 !important;
    cursor: auto !important;
    font-family: Lato !important;
    font-size: 15px !important;
    font-weight: 500 !important;
    line-height: 18px !important;
    text-align: left !important;
  }
}
