.discount-request {
  height: calc(100% - 121px);
  display: flex;
  flex-direction: column;
  .booking-tabs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 8px;

    .navigation {
      .nav-link {
        padding-bottom: 4px 10px;
        font-size: 14px;
        &:hover {
          color: #7d63ec !important;
        }
        &.active {
          font-weight: 700;
          color: #7d63ec !important;
          border-bottom: 3px solid #7d63ec !important;
          &:focus,
          &:focus-visible {
            color: #7d63ec !important;
          }
        }
      }
      .nav-tabs {
        border-bottom: none !important;
      }
    }
    .header-separator {
      border-bottom: 0.5px solid #e7e4ff;
      flex-grow: 1;
    }
  }
  .discount-request-container,
  .request-booking-container {
    display: flex;
    flex-direction: column;
    gap: 22px;
    margin-top: 22px;
    overflow-y: auto;
    height: calc(100% - 60px);
    .booking-details-container {
      height: calc(100% - 60px);
      // overflow-y: auto;

      .booking-details-accordion {
        .accordion-item {
          &.active {
            border: 0.1px solid #e4e5eb !important;
            box-shadow: 0px 4px 8px 0px #0a16290a !important;
            border-radius: 8px !important;
            .accordion-header {
              .accordion-button {
                background-color: #fff !important;
                border-radius: 8px !important;
              }
            }
          }
          .accordion-header {
            font-size: 16px;
            .accordion-button {
              background-color: #fbfafd !important;
              border-radius: 4px !important;
            }
          }

          border: none !important;
          border-radius: 4px !important;
          .booking-venue {
            .venue-info-label {
              font-size: 13px;
              font-weight: 400;
              color: #888888;
            }
            .venue-info-value {
              font-size: 14px;
              font-weight: 500;
              color: #282828;
            }
          }
        }

        .slot-data {
          .booking-info {
            .header {
              display: flex;
              justify-content: center;
              align-items: center;
              .heading {
                font-size: 16px;
                font-weight: 700;
                line-height: 24px;
                text-align: left;
                color: #5a557a;
              }
              .line-diff {
                border-top: 0.5px solid #e7e4ff;
                flex-grow: 1;
                margin-left: 8px;
              }
            }
            .header-text {
              font-size: 15px;
              font-weight: 600;
              line-height: 24px;
              text-align: left;
              color: #323232;
            }
          }
          //package-info
          .package-item {
            box-shadow: 0px 2px 6px 0px #0000000f;
            cursor: pointer;
            padding: 18px;
            border-radius: 8px;

            .label {
              font-size: 13px;
              font-weight: 400;
              line-height: 18px;
              color: #888888;
              margin-bottom: 6px;
            }

            .desc {
              font-size: 14px;
              font-weight: 500;
              line-height: 18px;
              color: #282828;
            }
          }
          .booking-data {
            .info-label {
              font-size: 13px;
              font-weight: 400;
              color: #888888;
            }
            .info-value {
              font-size: 14px;
              font-weight: 500;
              color: #282828;
            }
          }
          .venue-data {
            .info-label {
              font-size: 13px;
              font-weight: 400;
              color: #888888;
            }
            .info-value {
              font-size: 14px;
              font-weight: 500;
              color: #282828;
            }
          }
        }

        .amenities {
          padding: 14px 18px 18px 18px;
          border-radius: 8px 0px 0px 0px;
          background: #ffffff;
          gap: 8px;
        }
      }

      .accordion-button {
        &::after {
          margin-left: auto !important;
        }
      }

      .slot-date {
        display: flex;
        align-items: center;
        gap: 6px;
      }
    }
    .discount-request-details {
      padding: 24px 18px 24px 18px;
      gap: 10px;
      border-radius: 8px;
      background: #ffffff;
      display: flex;
      flex-direction: column;
      gap: 28px;
      box-shadow: 0px 2px 6px 0px #00000008;
      .disc-header {
        font-size: 14px;
        font-weight: 400;
        line-height: 18px;
        text-align: left;
        color: #888888;
      }
      .description {
        font-size: 15px;
        font-weight: 500;
        line-height: 18px;
        text-align: left;
        color: #323232;
      }

      .updated-billing-summary,
      .current-billing-summary {
        background: #fbfafd;
        padding: 12px 16px;
        gap: 40px;
        border-radius: 4px;
        flex-wrap: wrap;
        .disc-header {
          font-size: 13px;
          font-weight: 400;
          line-height: 18px;
          text-align: left;
          color: #888888;
        }
        .description {
          font-size: 14px;
          font-weight: 500;
          line-height: 18px;
          text-align: left;
          color: #58596b;
        }
      }
      .discount-type {
        display: flex;
        flex-direction: row;
        gap: 32px;
      }

      .billing-header {
        font-size: 12px;
        font-weight: 400;
        line-height: 14px;
        text-align: left;
        color: #888888;
      }
    }
  }

  .request-billing-container {
    margin-top: 22px;
    overflow-y: auto;
    height: calc(100% - 60px);
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
  input {
    opacity: 0;
    width: 0;
    height: 0;
  }
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;  
  right: 0;
  bottom: 0;
  background-color: #efeff1;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background: #A49ED7;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #7d63ec;
}

// input:focus + .slider {
//   box-shadow: 0 0 1px #7d63ec;
// }

input:checked + .slider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
  background-color: #ffffff;
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
