.custom-pagination{display:flex;justify-content:space-between;margin-top:20px;width:100%}.custom-pagination .record-text{font-size:15px;font-weight:400;line-height:18px;color:#323232 !important}.custom-pagination .arrow-disabled{opacity:.4}.custom-pagination .first-page::before,.custom-pagination .last-page::before{font-family:"e-icons";font-weight:bold;font-size:12px;color:#8181a5}.custom-pagination .first-page::before{content:""}.custom-pagination .last-page::before{content:""}.custom-pagination .first-page:hover,.custom-pagination .last-page:hover{background:rgba(94,129,244,.1) !important;color:#7d63ec !important}.custom-pagination .pagination{display:flex;list-style:none;padding:0}.custom-pagination .pagination li{margin-right:8px;cursor:pointer}.custom-pagination .pagination li.active a{background:rgba(94,129,244,.1) !important;color:#7d63ec !important;background:#fff;border-right-color:#e0e0e0;color:#000;border-radius:6px;padding:10px 15px;transition:.2s all}.custom-pagination .pagination li a{background:#fff;border-right-color:#e0e0e0;color:#1c1d21;border-radius:6px;padding:10px 15px;font-weight:700}.custom-pagination .pagination li a:hover{background:rgba(94,129,244,.1) !important;color:#7d63ec !important;border-right-color:#e0e0e0;border-radius:6px;padding:10px 15px;transition:.2s all}.custom-pagination .pagination li.previous,.custom-pagination .pagination li.next{margin-right:10px;cursor:pointer}.custom-pagination .pagination li.previous a,.custom-pagination .pagination li.next a{text-decoration:none;padding:10px 15px;font-size:14px;font-family:"Lato";color:#8181a5;font-weight:bolder}.custom-pagination .pagination li.previous a:hover,.custom-pagination .pagination li.next a:hover{background:rgba(94,129,244,.1) !important;color:#7d63ec !important;border-right-color:#e0e0e0;border-radius:6px;padding:10px 15px;transition:.2s all}.custom-pagination .pagination li.disabled,.custom-pagination .pagination li.disabled{opacity:.4}