.main-headings {
  font-size: 22px;
  font-weight: 600;
}
.desc-titles {
  font-size: 14px;
  color: #8181a5;
}
.contact-details {
  font-size: 16px;
  color: #38384e;
}
.setting-accordion .accordion-button::after {
  display: none;
}

.setting-accordion .accordion-button::before {
  content: "x";
  font-size: 24px;
  line-height: 1;
  margin-right: 25px;
  color: #7d63ec;
  transition: transform 0.2s ease-in-out;
}

.setting-accordion .accordion-button.collapsed::before {
  content: "\002B";
  transform: rotate(0);
  color: #7d63ec;
}

.setting-accordion .accordion-button:not(.collapsed) {
  color: #7d63ec;
}

.setting-accordion .accordion-item {
  border: none;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 4px !important;
  padding: 3px 20px;
  margin-bottom: 10px;
}

.setting-accordion .accordion-header {
  margin-left: 10px;
}
.setting-accordion .accordion-button {
  color: #20184e !important;
  font-family: "Red Hat Bold", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 19.2px;
  text-align: left;
}
.setting-accordion .accordion-button:not(.collapsed) {
  background-color: #fff !important;
  color: #20184e !important;
  font-weight: 700;
  padding: 10px 0px !important;
}
.setting-accordion .accordion-body {
  padding: 10px 10px !important;
  color: #888888 !important;
  font-size: 14px;
  font-family: "Red Hat Regular", sans-serif;
  font-weight: 400;
  line-height: 25.2px;
}

.help-support-navbar {
  .custom-nav-tabs {
    display: flex;
    gap: 20px;
    .nav-link {
      font-size: 14px;
      border: none !important;
      color: #888888;
      padding: 0;
      padding-bottom: 6px;
      font-weight: 700;
      &:hover {
        color: #7d63ec !important;
      }
    }
    .nav-link.active {
      border-bottom: 3px solid #7d63ec !important;
      color: #7d63ec !important;
      font-weight: 600 !important;
      &:focus,
      &:focus-visible {
        background: none !important;
        color: #7d63ec !important;
        box-shadow: none !important;
      }
    }
  }
}
