.notification-list {
  height: calc(100% - 60px);
}

/* width */
.notification-list::-webkit-scrollbar {
  width: 4px;
}

/* Track */
.notification-list::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
.notification-list::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
.notification-list::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.notificatio-sidebar-75px {
  left: 75px !important;
}

.notificatio-sidebar-230px {
  left: 230px !important;
}

.notification-offcanvas-75px.show {
  left: 75px !important;
}

.notification-offcanvas-230px.show {
  left: 230px !important;
}

.notification-item {
  border: 1px solid #e4e5eb;
  border-radius: 8px;
  margin-bottom: 16px;
  padding: 8px 8px 8px 10px;
}
.notification-item:last-child {
  margin-bottom: 0px;
}

.notification-title-container {
  padding: 10px 10px 0px 10px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #fdfdfe;
}

.notification-details-vertical-gap {
  padding: 0px 10px;
  margin-top: 8px;
}

.notification-title {
  font-size: 13px;
  color: #433f5c;
}

.notification-status {
  margin-right: 4px;
}

.notification-status .accepted {
  color: #1cb161;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 14px;
  border-radius: 24px;
  background-color: #ebfef1;
}

.notification-status .completed {
  color: #1cb161;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 14px;
  border-radius: 24px;
  background-color: #ebfef1;
}

.notification-status .rejected {
  color: #eb5e5e;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 14px;
  border-radius: 24px;
  background-color: #ff68681a;
}

.notification-subtitle {
  font-family: "Lato";
  font-size: 13px;
  color: #58596b;
  font-weight: 600;
}

.notification-subtitle div:first-child {
  padding-right: 8px;
  padding-left: 0px;
  border-right: 2px solid #e4e5eb;
}

.notification-subtitle div {
  padding: 0px 8px;
  border-right: 2px solid #e4e5eb;
}

.notification-subtitle div:last-child {
  padding-left: 8px;
  border: none;
}

.notification-date {
  display: flex;
  gap: 5px;
  color: #666666;
  font-weight: 500;
  font-size: 12px;
  align-items: center;
}

.details-container {
  display: flex;
  gap: 40px;
}

.person-details .label {
  font-size: 10px;
  color: #888888;
  margin-bottom: 2px;
}

.person-details .icon-label {
  display: flex;
  align-items: center;
}

.person-details .person-name {
  font-size: 12px;
  color: #58596b;
  font-weight: 600;
}

.notification-last-time {
  font-size: 10px;
  color: #999b9f;
  font-weight: 400;
}

.duplicated-bg-container {
  position: relative;
  margin-top: 18px;
}

.duplicated-bg {
  padding: 16px 10px 10px 10px;
  background: #fbfafd;
  border-radius: 8px;
}

.duplicated-bg-container .top-status {
  position: absolute;
  top: -8px;
  left: 10px;
  padding: 2px 14px;
  border-radius: 8px;
  font-size: 10px;
  font-weight: bold;
}

.duplicated-bg-container .top-status.duplicate {
  color: #e5960a;
  background: #fff3dd;
}

.duplicated-bg-container .top-status.original {
  color: #5e81f4;
  background: #e9eeff;
}

.border-bottom-notification {
  border-bottom: 1px solid #eef5f6;
}

.general-reminder {
  margin-right: 4px;
}

.general-reminder .general {
  color: #5e81f4;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 14px;
  border-radius: 24px;
  background-color: #f0f4ff;
}

.reminder-description {
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
  color: #888888;
}

.duplicate-inquiry {
  padding: 16px 10px 10px 10px;
  gap: 6px;
  border-radius: 8px 0px 0px 0px;
  background: #fbfafd;
  position: relative;
  margin-top: 16px;
}

.duplicate-badge {
  width: 71px;
  height: 18px;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 14px;
  border-radius: 24px;
  background: #fff3dd;
  color: #e5960a;
  position: absolute;
  top: -8px;
}

.original-badge {
  width: 71px;
  height: 18px;
  color: #5e81f4;
  font-size: 10px;
  font-weight: bold;
  padding: 2px 14px;
  border-radius: 24px;
  background-color: #f0f4ff;
  position: absolute;
  top: -8px;
}
