.e-control-wrapper.slot-dropdown-wrapper {
  padding: 8px 16px !important;
}

.slot-dropdown-wrapper {
  background-color: #f5f4ff !important;
  font-size: 16px;
  border-radius: 7px !important;
  border-color: transparent !important;
  margin-bottom: 0px !important;
  .item-template-custom {
    padding: 7px 2px !important;
  }

  .e-active,
  .e-hover {
    .slot-label {
      font-size: 16px;
      font-weight: 800;
      color: #5c48cc !important;
    }
    .slot-date {
      color: #5c48cc !important;
      font-size: 16px;
      font-weight: 800;
      line-height: 16px;
    }
  }

  .e-dropdownbase {
    .e-list-item.e-hover {
      background-color: #f5f4ff !important;
    }
    .e-list-item.e-active {
      background-color: #f5f4ff !important;
      color: #5c48cc !important;
    }
    .e-list-item.e-item-focus {
      background-color: #fff !important;
    }
  }
  .e-ddl.e-popup {
    box-shadow: 1px -2px 2px -3px rgba(0, 0, 0, 0.2),
      0px 0px 3px 1px rgba(0, 0, 0, 0.14), 0 3px 8px 2px rgba(0, 0, 0, 0.12);
  }
  input.e-input {
    padding: 0px !important;
    margin-bottom: 0px !important;
    ::selection {
      background: #7d63ec !important;
    }
    font-size: 15px !important;
  }
  .e-input-value {
    display: flex !important;
    align-items: center !important;
  }
  .slot-label {
    font-size: 16px;
    font-weight: 800;
    color: #1c1d21;
  }
  .slot-date {
    color: #1c1d21;
    font-size: 16px;
    font-weight: 600;
    line-height: 16px;
  }
  .slot-date:last-child {
    padding-right: 30px !important;
  }
}
