.upload-wrapper {
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' stroke='%23E4E5EBFF' stroke-width='2' stroke-dasharray='6' stroke-dashoffset='0' stroke-linecap='square'/%3e%3c/svg%3e");
  border-radius: 6px;
  padding: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
  height: 139px;
}

.doc-image-uploader input {
  opacity: 0;
  position: relative;
  bottom: 56px;
  height: 137px;
  width: 100%;
  margin-top: -20px;
}

.uploaded-file {
  border: 1px solid #e4e5eb;
  /* margin-bottom: 20px;  */
}

.uploaded-file .top-wrapper {
  padding: 15px;
  border-bottom: 1px solid #e4e5eb;
  display: flex;
  justify-content: center;
}

.uploaded-file .bottom-wrapper {
  padding: 10px 15px;
}

.uploaded-file .dropdown-toggle::after {
  display: none;
}

.uploaded-file .dropdown-toggle.show,
.uploaded-file .dropdown-toggle:focus {
  border: none !important;
}
