.custom-modal .modal-title {
    font-size: 18px !important;
    font-weight: 600;
    color: #5a557a;
  }
  .custom-modal .modal-header {
    padding: 15px;
    /* border: none; */
  }
  .custom-modal .modal-footer {
    padding: 8px !important;
    border: none;
    background-color: #f5f5fa;
  }
  .custom-modal .btn-close {
    background-image: url(../../../../../../../../assets/img/close-btn.png);
    width: 20px;
    height: 20px;
    background-size: auto;
  }
  .custom-modal .btn-close:focus {
    box-shadow: none;
  }
  .custom-modal .modal-question {
    color: #323232;
    margin-top: 13px;
    font-weight: 700;
  }
  .custom-modal .modal-content-heading {
    font-size: 18px;
    color: #323232;
    margin-bottom: 15px;
  }
  .width-560px.modal-dialog {
    max-width: 600px !important;
  }
  .inquiry-found-card {
    border: 1px solid #e4e5eb;
    border-radius: 10px;
    padding: 15px;
  }
  .inquiry-found-card .inquiry-found-title {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e4e5eb;
    padding-bottom: 12px;
  }
  .inquiry-found-body {
    margin-top: 12px;
  }
  .inquiry-found-card .inquiry-found-title h6 {
    font-weight: bold;
    margin-bottom: 0;
  }
  .inquiry-found-card .inquiry-found-body .date-time-wrapper {
    font-size: 14px;
  }
  .popup-img {
    width: 100px;
  }
