.primary-information {
  .input-one-line {
    padding: 4px 0px !important;
  }
  .form-label {
    margin-bottom: 0px;
  }
  .bolder-col-right:not(:last-child) {
    border-right: 1px solid #e4e5eb;
  }
  .information-card {
    color: #282828;
    font-size: 15px;
    .form-control {
      font-size: 15px !important;
    }
    .title {
      margin-bottom: 0px;
      font-weight: 600;
      font-size: 20px;
    }
    .title-three {
      color: #323232;
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 0px;
    }
    .btn-sm {
      padding: 4px 20px !important;
      font-weight: 600 !important;
      font-size: 12px !important;
    }
    .custom-dropdown {
      .e-input-value {
        font-weight: 600;
        font-size: 15px !important;
      }
    }
  }
}

.modal-body {
  .description {
    color: #666666;
    font-size: 13px;
  }
  .type-text {
    font-size: 16px;
    font-weight: 600;
  }
}

.modal-footer {
  .btn.btn-sm {
    padding: 8px 20px !important;
    border-radius: 6px !important;
  }
}

.confirm-input {
  letter-spacing: 3px;
  font-size: 16px !important;
  text-align: center;
  font-weight: bold !important;
  &:focus {
    box-shadow: none !important;
    border-color: #e4e5eb !important;
  }
  &::placeholder {
    font-weight: 600;
    font-size: 16px !important;
    color: #2e324163 !important;
  }
}

.confirm-input.error {
  border-color: #eb5e5e;
  &:focus {
    box-shadow: none !important;
    border-color: #eb5e5e !important;
  }
}

.error-container-already-exist {
  background: #fbfafd;
  border-radius: 12px;
  color: #eb5e5e;
  display: flex;
  align-items: center;
}

.communication-email {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 8px 4px 8px;
  border-radius: 8px;
  border: 1px solid #e5960a;
  background: #fff6e6;
  .message {
    color: #e5960a;
    font-size: 15px;
    font-weight: 500;
  }
}

@media (max-width: 768px) {
  .primary-information {
    .bolder-col-right:not(:last-child) {
      border-right: none;
    }
  }
}
