.time-details {
  .day-column-color {
    color: #525361;
    font-size: 15px;
    font-weight: 600;
  }
  .day-width-col {
    min-width: 150px;
  }
  .time-slot-wrapper {
    width: 250px;
    margin-right: 35px;
  }
  .time-picker-container {
    min-width: 200px;
    max-width: 300px;
    gap: 8px;
    display: flex;
    align-items: center;
    height: 30px;

    span.items {
      width: 120px;
      flex-grow: 1;
      & > div {
        padding: 5px;
      }
    }
  }
  .single-slot-width {
    width: 280px;
  }
  .add-new-slot {
    width: 80px;
  }
}
