.filter-form {
  .e-dropdownlist::placeholder {
    color: #58596b !important; /* Example color, change as needed */
    opacity: 1;
  }
}

.FBPackages {
  display: flex;
  height: 100%;
  flex-direction: column;
  .nav-tabs.custom-nav-tabs {
    border: none;
  }

  .custom-tab-content-data {
    .custom-accordian {
      .accordion-button {
        color: #323232;
        font-size: 15px;
        font-weight: bold;
        .selected-count {
          color: #8181a5;
        }
      }

      .accordion-button:not(.collapsed) {
        background-color: #fff !important;
        color: #666666 !important;
      }

      .accordion-button:focus {
        border-color: #fff !important;
        box-shadow: none !important;
      }
    }

    .simple-table {
      .count-width-col {
        width: 150px;
      }
    }
    .add-on-count {
      color: #58596b;
      font-weight: 700;
      font-size: 15px !important;
      display: flex;
      justify-content: center;
      .inc-dec-container {
        width: 110px;
        height: 36px;
        padding: 8px;
        border-radius: 4px;
        border: 1px solid #e4e5eb;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 15px;
      }
    }
  }
}

#fbPackageFilter {
  background: #21c4c1;
  height: 18px;
  width: 18px;
  position: absolute;
  top: -6px;
  right: -6px;
  border-radius: 50%;
  color: white;
  font-size: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.acc-label {
  flex-grow: 1;
  font-size: 14px;
  color: #58596b;
}
