.venue-info-wrapper {
  .venue-tabs {
    .nav-link {
      padding-bottom: 2px;
      font-size: 14px;
      &:hover {
        color: #7d63ec !important;
      }
      &.active {
        font-weight: 700;
        color: #7d63ec !important;
        border-bottom: 3px solid #7d63ec !important;
        &:focus,
        &:focus-visible {
          color: #7d63ec !important;
        }
      }
    }
    .nav-tabs {
      border: none;
    }
  }

  .venue-details {
    .title {
      color: #5a557a;
      font-weight: 600;
      margin-right: 10px;
      white-space: nowrap;
    }
    .line {
      width: 100%;
      height: 1px;
      background-color: #e7e4ff;
      margin-right: 10px;
    }
    .accordion {
      margin-top: 10px;
      .accordion-item {
        border: none !important;
        .accordion-button{
          padding-left: 0px !important;
          padding-right: 0px !important;
        }
        .accordion-button:not(.collapsed) {
          background-color: transparent;
        }
      }
    }
    //package-info
    .package-item {
      box-shadow: 0px 2px 6px 0px #0000000f;
      cursor: pointer;
      padding: 18px;
      border-radius: 8px;

      .label {
        font-size: 13px;
        font-weight: 400;
        line-height: 18px;
        color: #888888;
        margin-bottom: 6px;
      }

      .desc {
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
        color: #282828;
      }
    }

    .venue-info {
      .venue-info-label {
        font-size: 13px;
        font-weight: 400;
        color: #888888;
      }
      .venue-info-value {
        font-size: 14px;
        font-weight: 500;
        color: #282828;
      }
    }
  }

  .border-bottom-venue-info {
    border-bottom: 1px solid #fbfafd;
  }

  .color-009896 {
    color: #009896;
    font-weight: 500;
    font-size: 13px;
  }

  .display-label-data {
    font-weight: 500;
    font-size: 15px;
  }

  /* Amenities Accordian  */
  .display-amenities-group {
    display: flex;
    column-gap: 32px;
    flex-wrap: wrap;
  }
}

/* Venue Info Accordian */
.venue-info-offcanvas {
  width: 500px !important;

  .display-label-data {
    font-weight: 500;
    font-size: 15px;
    color: #2e3241;
  }

  .info-custom-label {
    font-size: 13px;
    font-weight: 500;
    line-height: 18px;
    color: #009896;
  }

  .custom-input{
    display: flex;
    align-items: center;
    border: none !important;
    border-bottom: 1px solid #dee2e6 !important;
  }

  .pre-post {
    font-size: 15px;
    font-weight: 500;
    line-height: 18px;
    text-align: left;
    color: #1C1D21;
    ;
  }

  .venue-rental-calc-table-wrapper {
    padding: 12px 12px 8px 12px;
    border-radius: 4px;
    border: 1px solid #e7e4ff;

    .venue-rental-calc-table {
      margin-bottom: 0 !important;

      > tbody > tr > td,
      > tbody > tr > th,
      > tfoot > tr > td,
      > tfoot > tr > th,
      > thead > tr > td,
      > thead > tr > th {
        border: none;
      }

      > thead > tr > th {
        font-size: 12px;
        font-weight: 400;
        line-height: 16px;
        color: #888888;
      }

      > tbody > tr > td {
        font-size: 14px;
        font-weight: 500;
        line-height: 18px;
        color: #58596b;
      }

      > tfoot {
        border-top: 1px solid #dee2e6;

        > tr > td {
          font-family: Lato;
          font-size: 13px;
          font-weight: 700;
          line-height: 18px;
          color: #1c1d21;
        }
      }
    }
  }
}

.offcanvas-footer-content {
  background-color: #f5f5fa;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 70px;
  padding: 16px;
  div[role="button"] {
    font-size: 15px;
  }
}
