.sidebar {
  background-color: var(--primary-color);
  position: absolute;
  top: 0;
  height: 100%;
  transition: width 0.3s ease;

  .sidebar-logo-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70px;
    cursor: pointer;
  }

  .sidebar-list {
    margin-top: 15px;

    .dropdown-item {
      padding: 2px 8px !important;
      font-size: 13px;
      color: #5a557a;
    }

    .sidebar-list-item {
      display: flex;
      align-items: center;
      height: 40px;
      border-radius: 5px;
      margin: 10px 18px;
      padding: 0px 6px;
      text-decoration: none;
      transition: 0.2s width;

      .sidebar-item-title {
        color: white;
        padding-left: 10px;
      }

      &:hover {
        background: #644fbd;
      }

      .dropdown-menu {
        z-index: 2000;
      }
    }

    .sidebar-list-item.active {
      background: #644fbd;
    }

    .sub-menu-list {
      display: flex;
      align-items: center;
      justify-content: space-between;

      div {
        display: flex;
        align-items: center;
      }
    }
  }
}

.sidebar.sidebar-shrink {
  width: 75px;

  .sidebar-list {
    .sidebar-list-item {
      justify-content: center;

      .sidebar-item-title {
        display: none;
      }

      .settings-right-arrow {
        display: none;
      }

      .profile-details {
        display: none;
      }

      .dd-profile-shrink {
        left: 52px !important;
        bottom: 10%;
      }

      .dd-setting-shrink {
        left: 51px !important;
        top: 0 !important;
      }
    }

    .sub-menu-list {
      justify-content: center;
    }
  }
}

.sidebar.sidebar-wide {
  width: 230px;

  .sidebar-list .sidebar-list-item .dd-profile-wide {
    left: 206px !important;
    bottom: 10%;
  }

  .sidebar-list .sidebar-list-item .dd-setting-wide {
    left: 206px !important;
    top: 0 !important;
  }
}

.sidebar-profile {
  position: absolute;
  bottom: 20px;
  right: 0;
  left: 0;

  .profile-wrapper {
    img {
      border-radius: 5px;
    }
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    align-items: center;

    .profile-details {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-left: 10px;

      .name {
        color: #ffffff;
        font-size: 14px;
      }

      .sales {
        color: rgba(255, 255, 255, 0.342);
        font-size: 12px;
      }
    }
  }
}
.version-code {
  position: absolute;
  bottom: 7px;
  right: 0;
  left: 0;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  color: white;
  opacity: 0.7;
}

.sidebar-tooltip {
  .tooltip-inner {
    background-color: #4f3f95d1 !important;
  }
  &.bs-tooltip-end .tooltip-arrow::before {
    border-right-color: #4f3f95d1 !important;
  }
  &.bs-tooltip-bottom .tooltip-arrow::before {
    border-bottom-color: #4f3f95d1 !important;
  }
  &.bs-tooltip-top .tooltip-arrow::before {
    border-top-color: #4f3f95d1 !important;
  }
}

.notification {
  position: relative;

  .notification-count {
    position: absolute;
    top: 5px;
    right: 1px;
    background: #21c4c1;
    border-radius: 32px;
    width: 18px;
    height: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    color: white;
    font-weight: 700;
  }
}

