.duplicate-inquiry-modal {
  .checked {
    color: #323232 !important;
  }
  .border-right {
    border-right: 1px solid #eef5f6;
  }
  .modal-content-heading {
    font-size: 15px;
    color: #323232;
    margin-bottom: 15px;
    font-weight: 600;
  }
  .inquiry-found-card {
    border: 1px solid #e4e5eb;
    border-radius: 10px;
    padding: 15px;
    .inquiry-found-title {
      font-size: 16px;
      display: flex;
      align-items: center;
      border-bottom: 1px solid #e4e5eb;
      padding-bottom: 12px;
      h6 {
        font-weight: bold;
        margin-bottom: 0;
      }
    }
    .inquiry-found-body {
      margin-top: 12px;
      .duplicate-label {
        color: #888888;
        font-size: 12px;
        font-weight: 600;
      }
      .duplicate-label-value {
        color: #323232;
        font-size: 14px;
      }
      .slot-duplicate-text {
        color: #eb5e5e;
        font-size: 14px;
      }
      .slot-text {
        color: #888888;
        font-size: 14px;
      }
    }
  }
  .duplicate-inquiry .form-check label {
    color: #8181a5;
    font-size: 15px !important;
  }
}
