.label-for-venue-name {
  font-weight: 600;
  color: #8181a5;
  text-transform: capitalize;
  &.checked {
    color: #1c1d21;
  }
}
.scale-label {
  font-size: 13px;
  width: 120px;
  .icon {
    display: inline-block;
    width: 20px;
    text-align: center;
  }
}

.border-right-4-px {
  border-right: 4px solid #fbfafd;
}

.border-bottom-summary-selection {
  border-bottom: 1px solid #fbfafd;
  text-transform: capitalize;
}

.height-form-venue {
  height: calc(100% - 115px);
}

.closedVenue {
  &.venue-not-operational {
    background: #e5960a;
  }
  &.confirmed {
    background: #5e81f4;
  }
  background: #eb5e5e;
  padding: 4px 12px;
  color: white;
  border-radius: 24px;
  font-size: 13px;
  font-weight: 400;
}

.time-slot-wrap {
  display: flex;
  column-gap: 7px;
  row-gap: 4px;
  font-size: 13px;
  flex-wrap: wrap;
}
