.cancel-booking {
  height: calc(100% - 91px);
  display: flex;
  flex-direction: column;

  .booking-cancel-header {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 8px;
    .heading {
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      color: #5a557a;
    }
    .header-separator {
      border-bottom: 0.5px solid #e7e4ff;
      flex-grow: 1;
    }
  }
  .cancel-booking-form {
    margin-top: 22px;
    .form-label {
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      color: #888888;
    }
    .form-check-label {
      font-size: 15px;
      font-weight: 500;
      line-height: 18px;
      color: #1c1d21;
    }
  }
}
.cancel-booking-details {
  height: calc(100% - 121px);
  display: flex;
  flex-direction: column;
  .booking-cancellation {
    .details {
      padding: 14px 18px;
    }
    .details-heading {
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      text-align: left;
      color: #666666;
    }
  }
  .booking-alert {
    margin: 22px 0px !important;
  }
  .booking-cancel-header {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 8px;
    .heading {
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      color: #5a557a;
    }
    .header-separator {
      border-bottom: 0.5px solid #e7e4ff;
      flex-grow: 1;
    }
  }
}
