.quotation-main {
  height: calc(100% - 91px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;

  .quotation-header-container {
    margin-bottom: 22px;
    .title-header {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      flex-wrap: wrap;
      .title {
        color: #5a557a;
        font-weight: 700;
        font-size: 16px;
      }
      .line-diff {
        border-bottom: 0.5px solid #e7e4ff;
        flex-grow: 1;
      }
    }
    .quotation-dropdown-list {
      border: 1px solid #ececf2;
      .option-label {
        color: #323232;
        font-size: 15px;
      }
    }
    .btn-outline-primary:disabled {
      border: 1px solid #a2a2b1 !important;
      color: #a2a2b1 !important;
    }
  }

  .create-quotation-wrapper,
  .preview-quotation-wrapper {
    height: calc(100% - 98px);
    padding: 12px;
    overflow: auto;
    margin-top: 10px;
  }

  .create-quotation-wrapper,
  .share-quotation-wrapper {
    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      .heading {
        color: #5a557a;
        font-weight: 700;
        font-size: 16px;
      }
      .header-separator {
        background-color: #e4e5eb;
        width: 100%;
        height: 1px;
      }
    }
    .create-quotation-form {
      input[type="text"]:read-only,
      input[type="email"]:read-only {
        opacity: 0.4 !important;
        cursor: not-allowed !important;
      }
      .quotation-check-label {
        font-size: 15px;
        font-weight: 500;
        line-height: 18px;
        text-align: left;
        color: #8181a5;
      }
      .custom-input {
        display: flex;
        justify-content: space-between;
        .mb-country {
          width: 30%;
          padding: 1px;
          bottom: 0px;
        }
        .mb-input {
          width: 70%;
        }
      }
    }
  }

  .share-quotation-wrapper {
    .share {
      font-size: 15px;
      font-weight: 400;
      line-height: 18px;
      color: #323232;
    }
    .share-text {
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      color: #8181a5;
    }

    .email-wrapper {
      display: flex;
      gap: 8px;
      flex-wrap: wrap;
      .multi-email-input {
        display: flex;
        flex-direction: column;
        min-width: 60%;

        label {
          font-size: 14px;
          margin-bottom: 8px;
        }

        .email-list {
          display: flex;
          flex-wrap: wrap;
          align-items: center;
          border-radius: 8px;
          gap: 8px;
          padding: 4px 8px;
          border: 1px solid #e4e5eb;
        }

        .email-item {
          background-color: #ede7ff;
          padding: 4px 12px;
          border-radius: 24px;
          display: flex;
          align-items: center;
          font-size: 13px;
          font-weight: 500;
          color: #323232;
        }

        .remove-email {
          margin-left: 5px;
          cursor: pointer;
          font-weight: bold;
        }

        input {
          border: none;
          outline: none;
          flex: 1;
          padding: 8px;
          font-size: 15px;
          margin-left: 5px;
          border-radius: 8px;

          ::placeholder {
            color: #e4e5eb;
            font-size: 15px;
            font-weight: 500;
          }
        }

        .email-item-error {
          border: 1px solid #eb5e5e !important;
          background: #eb5e5e1a !important;
        }

        .error-msg {
          font-size: 14px;
          font-weight: 400;
          line-height: 18px;
          color: #eb5e5e;
        }
      }

      .share-button {
        padding: 13px 16px;
        gap: 8px;
        border-radius: 6px;
      }
    }
  }

  .preview-quotation-wrapper {
    // border: 1px solid rgb(245, 242, 242);
    // box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    //   rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    .content {
      padding: 30px;
      h3 {
        color: #2e3241;
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
      }
      .address {
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        color: #2e3241;
        .name {
          font-size: 20px;
          font-weight: 700;
          line-height: 30px;
          color: #58596b;
        }
        .details {
          font-size: 18px;
          font-weight: 400;
          line-height: 30px;
          color: #666666;
        }
      }
      .note {
        font-size: 18px;
        font-weight: 500;
        line-height: 30px;
        color: #58596b;
        margin: 30px 0px;
      }
      .heading {
        font-size: 18px;
        font-weight: 700;
        color: #272437;
      }
      .additional-details {
        font-size: 16px;
        font-weight: 400;
        line-height: 26px;
        color: #666666;
        text-align: left;
      }
      .payment-terms {
        font-size: 13px;
        font-weight: 400;
        line-height: 20px;
        color: #666666;
        li {
          margin-top: 10px;
        }
      }
      .signature {
        color: #979797b2;
      }
      .venue-information {
        border: 1px solid #e4e5eb;
        border-radius: 4px;
        .venue-header {
          background: #edf1ff;
          font-size: 14px;
          font-weight: 700;
          line-height: 18.52px;
          text-align: left;
          text-transform: uppercase;
          padding: 10px 12px;
        }
        table {
          width: 100%;
          border-collapse: collapse;

          th,
          td {
            border: 1px solid #e4e5eb;
            padding: 8px;
            text-align: left;
          }

          th {
            color: #000000;
            padding: 10px 12px;
            font-size: 14px;
            font-weight: 700;
          }

          td {
            font-size: 15px;
            font-weight: 400;
            color: #58596b;
            padding: 10px 12px;
          }
        }
      }
      .amenities-item {
        .venue {
          font-size: 15px;
          font-weight: 500;
          line-height: 18px;
          color: #272437;
        }
        .amenities {
          font-size: 16px;
          font-weight: 400;
          line-height: 28px;
          color: #666666;
        }
      }
      .charges-italic {
        font-size: 16px;
        font-style: italic;
        font-weight: 500;
        line-height: 18px;
        text-align: left;
        color: #1c1d21;
      }
      .heading-package {
        background: #edf1ff;
        font-size: 14px;
        font-weight: 700;
        line-height: 18.52px;
        text-align: left;
        text-transform: uppercase;
        padding: 10px 12px;
      }
      .package-info {
        border-radius: 4px;
        .package-info-header {
          padding: 10px 0px 10px 0px;
          gap: 40px;
          background: #fbfafd;
          border: 1px solid #e4e5eb;
          flex-wrap: wrap;
        }
        .data-label {
          text-wrap: nowrap;
        }

        .package-item {
          border: 1px solid #e4e5eb;
          color: #666666;
        }
      }
    }
  }
}

.quotation-list-container {
  height: 100%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;

  .quotation-list-wrapper {
    .top-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      margin-bottom: 22px;
      .header-separator {
        background-color: #e4e5eb;
        width: 100%;
        height: 1px;
      }
      .quotation-list-heading {
        font-size: 16px;
        font-weight: 700;
        line-height: 24px;
        color: #5a557a;
      }
    }
    .quotation-list {
      height: calc(100% - 65px) !important;
      .e-grid {
        height: calc(100% - 34px) !important;
      }

      .e-grid .e-gridcontent {
        height: 100% !important;
      }
      .e-grid .e-content {
        height: 100% !important;
      }

      .e-grid.e-responsive .e-headercelldiv {
        text-overflow: initial !important;
        white-space: initial !important;
      }
    }
  }
}

.quotation-success {
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  text-align: center;
  color: #5a557a;
  margin-bottom: 24px;
}

.quotation-success-modal {
  .modal-content {
    width: 400px !important;
  }
  gap: 8px;
  img {
    width: 194px;
    height: 168px;
  }
  .modal-body {
    padding: 20px 24px 38px 24px !important;
    width: 400px !important;
    height: 369px;
  }
}

.create-quotation-stepper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  .line {
    height: 2px;
    width: 50px;
    background-color: #e1e1e1;
    margin: 0 10px;
  }

  .progress-item {
    display: flex;
    align-items: center;
    justify-content: center;

    .icon-wrapper {
      border: 1px solid #e1e1e1;
      border-radius: 50px;
      div {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    &.active {
      .icon-wrapper {
        background-color: #7d63ec;
        border: 1px solid #7d63ec;
      }
      .title {
        color: #7d63ec;
      }
    }
    &.done {
      .icon-wrapper {
        background-color: #008381;
        border: 1px solid #008381;
      }
      .title {
        color: #008381;
      }
    }
    &.pending {
      .title {
        color: #e1e1e1;
      }
    }
  }
}

.billingSummary {
  .billing-header {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    .title {
      font-size: 16px;
      font-weight: bold;
      color: #5a557a;
    }
    .line-diff-billing {
      border-bottom: 0.5px solid #e7e4ff;
      flex-grow: 1;
    }
  }
}
.summary-extra-charges-form {
  .summary-extra-charges-container {
    padding: 16px;
    background: #f5f4ff;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    border-radius: 4px;
    .summary-extra-charges {
      display: flex;
      gap: 30px;
    }
    .label-title {
      color: #888888;
      font-size: 13px;
      margin-bottom: 3px;
    }
    .label-value {
      color: #323232;
      font-size: 15px;
      font-family: "Lato Medium";
    }
  }
  .item-container {
    display: flex;
    gap: 20px;
    .chargesItemWidth {
      min-width: 95px;
      max-width: 140px;
    }
  }
  .label-for-minimum-revenue {
    &.checked {
      color: #1c1d21;
    }
    color: #8181a5;
    font-family: "Lato Medium";
    font-size: 15px;
  }
  .form-control {
    &:focus {
      color: #282828;
      font-family: "Lato Medium";
    }
    color: #282828;
    font-family: "Lato Medium";
  }
}

.single-option {
  font-family: "Lato Medium";
  font-size: 15px;
  color: #5a557a;
  margin-bottom: 15px;
}

.quotation-simple-table-summary {
  .total-final-amount {
    td {
      background: #e4e5eb;
      color: #1c1d21;
      font-size: 16px !important;
      font-weight: 700 !important;
      line-height: 18px !important;
      text-align: left;
    }
  }
  thead.bold-header tr th {
    font-family: "Lato Medium";
    text-transform: uppercase;
    font-size: 14px;
    color: #323232;
  }

  .slot-list-data {
    tr:last-child {
      td {
        // border-bottom: 0.5px solid #7676b1;
      }
    }
  }

  thead tr {
    border-radius: 4px;
    height: 42px;
    th {
      word-spacing: 3px;
      text-transform: uppercase;
      vertical-align: middle;
      white-space: nowrap;
      border-bottom: 1px solid #d4cfff !important;
      background: #f9f9ff;
      color: #1c1d21;
      font-size: 14px;
      padding: 8px 16px;
    }
  }
  tbody tr {
    height: 36px;
    td {
      color: #525361;
      font-weight: 500;
      font-size: 14px;
      border-bottom: 1px solid #e4e5eb;
      padding: 8px 16px;
      align-content: center;
    }
  }
  .slot-row-data {
    color: #171717;
    font-size: 14px;
    font-weight: bold;
  }
  .venue-row-data {
    td {
      background-color: #fbfafd;
      font-family: "Lato Bold";
      color: #323232;
      font-style: italic;
    }
  }
  .sub-total-row-data {
    td {
      background-color: #e4e5eb !important;
      font-family: "Lato Bold" !important;
      color: #323232 !important;
      border: 1px solid #e4e5eb;
    }
  }
  .option-menu-col {
    width: 50px;
  }
  .col-80 {
    width: 80px;
  }
  .col-150 {
    width: 150px;
  }
  .col-200 {
    width: 200px;
  }
}

.billing-summary-container {
  padding: 12px 16px;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  &.current {
    background: #fdfdfe;
    .label-title {
      opacity: 0.5;
    }
  }
  &.updated {
    background: #fbfafd;
  }
  .discount {
    display: flex;
    gap: 30px;
  }
  .label-title {
    color: #888888;
    font-size: 13px;
    margin-bottom: 3px;
  }
  .label-value {
    color: #323232;
    font-size: 15px;
    font-family: "Lato Medium";
  }
  .item-container {
    display: flex;
    gap: 20px;
    .chargesItemWidth {
      min-width: 95px;
      max-width: 140px;
    }
  }
}
