.accounts-venue-details {
  .tab-title {
    color: #1c1d21;
    font-size: 18px;
    font-weight: bold;
  }
  .loader-height {
    height: 200px;
  }
}
.delete-input {
  letter-spacing: 3px;
  font-size: 16px !important;
  text-align: center;
  font-weight: bold !important;
  &:focus {
    box-shadow: none !important;
    border-color: #e4e5eb !important;
  }
  &::placeholder {
    font-weight: 600;
    font-size: 16px !important;
    color: #2e324163 !important;
  }
}

.delete-input.error {
  border-color: #eb5e5e;
  &:focus {
    box-shadow: none !important;
    border-color: #eb5e5e !important;
  }
}

// status text
.active-text {
  color: #009896;
  font-weight: bold;
}
.inactive-text {
  color: #e5960a;
  font-weight: bold;
}
.deleted-text {
  color: #eb5e5e;
  font-weight: bold;
}
.blue-text {
  color: #5E81F4;
  font-weight: bold;
}
.css-in-active-icon {
  background: url("./../../../../../assets/img/venue-check-box-list.svg")
    no-repeat center center/cover;
  width: 25px !important;
  margin: 6px;
}

.css-delete-icon {
  background: url("./../../../../../assets/img/venue-list-delete.svg") no-repeat
    center center/cover;
  width: 25px !important;
  margin: 6px;
}
