.calender {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.summary-detail-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .no-data-summary {
    font-size: 16px;
    font-weight: bold;
    color: #666666;
  }
}

.text-green {
  color: #1cb161;
}

.summary-detail-groups {
  height: 100%;
  width: 100%;
  padding: 0 20px;
  border-left: 1px solid #c2c8d6;
  overflow-y: auto;

  .month-info {
    h6 {
      font-weight: bold;
      font-size: 18px;
      color: #323232;
      margin-bottom: 4px;
    }
    p {
      color: #666666;
      font-size: 12px;
      margin-bottom: 0px;
    }
  }

  .month-heading {
    font-size: 14px;
    color: #8181a5;
    font-weight: bold;
  }
}
.summary-heading-wrapper {
  &.headingTitle {
    padding: 12px 0 12px 20px;
  }
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-left: 1px solid #c2c8d6;
  height: 100%;
  border-bottom: 1px solid #c2c8d6;
  .summary-headings {
    font-weight: bold;
    color: #323232;
    margin-bottom: 0px;
  }
  .heading-date {
    color: #7d63ec;
    margin-bottom: 0px;
  }
}

.operational-desc {
  color: #888888;
  font-size: 14px;
}

.operational-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 15px;
}

.reminder-card-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.calendar-reminder-card {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.card-border {
  border-bottom: 0.5px solid #ebebf5;
}

.reminder-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 15px;
  .reminder-card-title {
    font-size: 13px;
    font-weight: 700;
    // line-height: 18px;
    color: #433f5c;
  }
}

.reminder-badge {
  background-color: #f5f4ff;
  padding: 2px 8px;
  border-radius: 24px;
  font-size: 10px;
  margin-left: 10px;
}

.reminder-date {
  font-size: 12px;
  font-weight: 500;
  color: #666666;
}

.calender-reminder-desc {
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
  color: #888888;
}

.reminder-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reminder-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.upcoming-badge {
  background-color: #f19a031a;
  color: #f19b03;
  padding: 8px 15px;
  border-radius: 25px;
  font-size: 13px;
}

.user-desc {
  color: #58596b;
  font-size: 14px;
}

.num-digit {
  color: #8181a5;
  margin-left: 10px;
}

.booking-heading p {
  font-size: 15px;
  font-weight: 600;
}

.e-schedule .e-month-view .e-appointment:focus,
.e-schedule .e-month-view .e-appointment.e-appointment-border {
  box-shadow: none !important;
}

.e-calendar .e-content td.e-selected span.e-day,
.e-bigger.e-small .e-calendar .e-content td.e-selected span.e-day {
  background-color: #fff !important;
  color: #7d63ec !important;
}
.e-btn.e-flat.e-primary,
.e-css.e-btn.e-flat.e-primary {
  color: #7d63ec !important;
}

.e-btn.e-flat.e-primary:hover,
.e-css.e-btn.e-flat.e-primary:hover {
  color: #7d63ec !important;
}

.e-schedule .e-month-view .e-date-header-wrap table col {
  width: 110px !important;
}

.e-schedule .e-month-view .e-date-header-wrap table td,
.e-schedule .e-month-agenda-view .e-date-header-wrap table td {
  background: #f7f5fd !important;
  border-color: rgba(0, 0, 0, 0.12);
  border-style: solid;
  border-width: 0 0 1px 1px;
  color: rgba(0, 0, 0, 0.87);
  font-size: none;
  text-align: center !important;
  text-transform: none;
  text-align: center;
  border-top: none !important;
  padding: 10px;
}

.e-schedule
  .e-schedule-toolbar
  .e-toolbar-items
  .e-toolbar-item.e-date-range
  .e-tbar-btn
  .e-tbar-btn-text {
  font-size: 18px !important;
  font-weight: 600 !important;
}
.e-schedule .e-schedule-toolbar {
  box-shadow: none !important;
  margin-bottom: 10px !important;
  min-height: 45px !important;
}

.e-schedule {
  border: 0px !important;
}

.e-schedule .e-month-view .e-work-cells {
  height: 150px !important;
  width: 110px !important;
}

.e-schedule .e-month-view .e-work-cells:hover {
  background: transparent !important;
}

.e-schedule .e-month-view .e-appointment {
  background: #fff !important;
  border-top: none !important;
  border-left: 0 !important;
  border-right: 0 !important;
  border-bottom: 0 !important;
  border-radius: 0px !important;
  height: 45px !important;
  padding-bottom: 5px !important;
}

.e-more-popup-wrapper .e-appointment {
  height: 45px !important;
  background: #fff !important;
  margin-bottom: 5px !important;
  display: block !important;
  cursor: auto !important;
  & > div {
    height: 100%;
  }
  .event-custom-calender {
    margin: 0px !important;
  }
}
.e-more-popup-wrapper .e-appointment.e-appointment-border,
.e-more-popup-wrapper .e-appointment:focus {
  box-shadow: none !important;
}

.e-schedule .e-month-view .e-selected-cell,
.e-schedule .e-month-agenda-view .e-selected-cell {
  background: #fff !important;
  border: 1px solid #7d63ec !important;
}

.e-selected-cell-custom {
  background: #fff !important;
  border: 1px solid #7d63ec !important;
}

.e-schedule .e-month-view .e-current-date .e-date-header,
.e-schedule .e-month-agenda-view .e-current-date .e-date-header {
  background: #009896 !important;
  border-radius: 4px !important;
  width: fit-content !important;
  padding: 0px !important;
  margin: 5px auto 0px !important;
  .dateHeader .date-text-month {
    color: white !important;
    padding: 1px 4px;
    margin-top: -8px;
  }
}

.e-schedule .e-text-center {
  text-align: left !important;
}

.e-schedule .e-month-view .e-appointment .e-appointment-details .e-subject {
  padding: 1px 8px !important;
}
.e-schedule .e-month-view .e-appointment .e-appointment-details {
  & > div {
    height: 100%;
  }
  display: block !important;
}

.e-schedule .e-month-view .e-date-header-wrap table td.e-current-day,
.e-schedule .e-month-agenda-view .e-date-header-wrap table td.e-current-day {
  color: #7d63ec !important;
}
.e-schedule .e-month-view .e-work-cells:hover,
.e-schedule .e-month-agenda-view .e-work-cells:hover,
.e-schedule .e-month-view .e-work-cells:active,
.e-schedule .e-month-agenda-view .e-work-cells:active {
  border: 1px solid #7d63ec !important;
}

.venue-headings {
  font-size: 16px;
  font-weight: 500;
}
.e-schedule .e-timeline-month-view .e-resource-left-td {
  width: 150px !important;
}

.e-schedule .e-timeline-view .e-appointment .e-subject,
.e-schedule .e-timeline-month-view .e-appointment .e-subject {
  display: none !important;
}
.e-schedule .e-timeline-view .e-appointment .e-time,
.e-schedule .e-timeline-month-view .e-appointment .e-time {
  display: none !important;
  line-height: 1.4;
}
.e-schedule .e-timeline-month-view .e-appointment {
  border-radius: 4px !important;
  height: 30px !important;
}

.e-schedule .e-timeline-view .e-work-cells,
.e-schedule .e-timeline-month-view .e-work-cells {
  border-color: #fff !important;
}

.e-schedule .e-timeline-month-view .e-date-header-wrap table td {
  border-color: white !important;
}

.e-schedule .e-timeline-month-view .e-content-wrap table col {
  width: 50px !important;
}

.e-schedule .e-timeline-month-view .e-date-header-wrap table col {
  width: 50px !important;
}

.e-schedule .e-timeline-month-view .e-resource-column-table {
  height: initial !important;
}

.e-schedule
  .e-timeline-month-view
  .e-work-cells:hover:not(.e-resource-group-cells) {
  background: #fff !important;
}

.e-toolbar .e-toolbar-items {
  background: white !important;
}

.e-toolbar .e-toolbar-item .e-tbar-btn {
  background: white !important;
}

.e-schedule .e-schedule-toolbar {
  margin-bottom: 40px !important;
}

.e-schedule .e-month-view .e-date-header:hover {
  text-decoration: none !important;
}

.e-schedule .e-month-view .e-date-header {
  cursor: auto !important;
  height: 24px !important;
  margin: 0px !important;
  text-align: center;
  width: 100% !important;
}

.column {
  overflow: hidden;
  gap: 1.5px;
  width: 40px;
  height: 55px;
  display: flex;
  flex-direction: column;
  transition: 0.1s all;
  border-radius: 4px;
  border: 1px solid #e4e5eb;
  &.closed {
    border: 1px solid #e4e5eb;
  }
  &:hover {
    border: 1.5px solid #8b6ffa;
  }
  &.selected {
    border: 1.5px solid #8b6ffa;
  }
}

.section {
  flex: 1;
}

.booking {
  background-color: #c8b6ff;
}

.not-operational {
  background-color: #ff9f9f;
}

.available {
  background-color: #ffffff;
}

.closed {
  background-color: #f8f8f9;
}

.dateHeader {
  .date-text {
    color: black;
    font-size: 15px;
    font-weight: bold;
    text-align: center;
  }
  .date-text-month {
    color: black;
    font-size: 13px;
    font-weight: bold;
    text-align: center;
  }
  .day-text {
    color: #888888;
    font-size: 13px;
    text-align: center;
  }
}

.event-custom-calender {
  background: #f9f9ff;
  border-radius: 3px;
  font-size: 10px;
  border-top: 3px solid #a384ff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
  margin: 4px;
  padding: 5px 7px;
  color: #7d63ec;
  height: 100%;
  &.not-operational {
    border-top: 3px solid #ff8686;
    background: #fff5f5;
    color: #d44952;
  }
  .purpose-text {
    font-weight: bold;
    line-height: 1.2;
  }
  .purpose-sub-text {
  }
}

.calender-tooltip {
  .tooltip-inner {
    background-color: #4f3f95d1 !important;
  }
  &.bs-tooltip-end .tooltip-arrow::before {
    border-right-color: #4f3f95d1 !important;
  }
  &.bs-tooltip-bottom .tooltip-arrow::before {
    border-bottom-color: #4f3f95d1 !important;
  }
  &.bs-tooltip-top .tooltip-arrow::before {
    border-top-color: #4f3f95d1 !important;
  }
}

.calender-header-custom {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  .month-navigate {
    display: flex;
    align-items: center;
    .arrow {
      padding: 8px;
      display: flex;
    }
    .calender-month-select {
      position: relative;
      .month-select {
        padding: 8px;
        position: absolute;
        inset: 0;
        opacity: 0;
      }
      .month-year-name {
        display: flex;
        padding: 8px;
        gap: 10px;
        font-size: 20px;
        line-height: normal;
        .month {
          color: #343a3b;
          font-weight: bolder;
        }
        .year {
          color: #7d63ec;
          font-weight: bold;
        }
      }
    }
  }

  .calender-event-wrapper {
    display: flex;
    justify-content: end;
    gap: 20px;
    flex-wrap: wrap;
    .calender-event {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      h6 {
        font-size: 13px;
      }
      .indicate-box1 {
        background-color: #fff;
        width: 16px;
        height: 16px;
        border: 1px solid #d7dedf;
        border-radius: 4px;
      }

      .indicate-box2 {
        background-color: #c8b6ff;
        width: 16px;
        height: 16px;
        border: 1px solid #c8b6ff;
        border-radius: 4px;
      }

      .indicate-box3 {
        background-color: #ff8686;
        width: 16px;
        height: 16px;
        border: 1px solid #ff8686;
        border-radius: 4px;
      }

      .indicate-box4 {
        background-color: #f1f1f8;
        width: 16px;
        height: 16px;
        border: 1px solid #d7dedf;
        border-radius: 4px;
      }
    }
  }
}

.calender-acc {
  .title {
    color: #5a557a;
    font-weight: 600;
    margin-right: 10px;
    white-space: nowrap;
  }
  .line {
    width: 100%;
    height: 1px;
    background-color: #e7e4ff;
    margin-right: 10px;
  }
  .accordion {
    margin-top: 10px;
    .accordion-item {
      border: none !important;
      .accordion-button {
        padding-left: 0px !important;
        padding-right: 0px !important;
      }
      .accordion-button:not(.collapsed) {
        background-color: transparent;
      }
    }
  }
}

.day-stats {
  padding: 8px 20px 16px;
  background: #fbfafd;
  border-radius: 8px;
}

.calender-summary-accordion {
  .accordion-item {
    border: none !important;
    border-radius: 4px !important;
    &.active {
      border: 0.1px solid #e4e5eb !important;
      box-shadow: 0px 4px 8px 0px #0a16290a !important;
      border-radius: 8px !important;
      .accordion-header {
        .accordion-button {
          background-color: #fff !important;
          border-radius: 8px !important;
        }
      }
    }
    .accordion-header {
      font-size: 16px;
      .accordion-button {
        background-color: #fbfafd;
        border-radius: 4px !important;
        .acc-title {
          font-weight: bold;
          font-size: 14px;
          color: #666666;
          text-transform: uppercase;
          margin-bottom: 0px;
        }
        &::after {
          margin-left: auto !important;
        }
        .count {
          color: #8181a5;
          font-size: 14px;
          font-family: "Lato Medium";
          margin-left: 5px;
        }
      }
    }
  }
}

.calendar-select {
  .custom-dropdown.bordered {
    border-radius: 8px !important;
    border-color: #7d63ec !important;
  }
}

.border-calender {
  height: auto !important;
  padding: 6px 3px !important;
  min-width: 180px !important;
  border-color: #7d63ec !important;
  .e-input-value {
    font-size: 15px !important;
    font-weight: bold !important;
    color: #7d63ec !important;
  }
}

.tooltip-calender {
  .title {
    color: #7676b1;
    font-weight: bold;
    font-size: 13px;
  }
  .slot-time {
    color: #58596b;
    font-size: 11px;
  }
}
.ant-tooltip-arrow {
  right: 4px !important;
}

.left-col,
.right-col {
  height: calc(100% - 52px) !important;
  overflow-y: auto !important;
}

.e-schedule .e-timeline-month-view .e-resource-text {
  text-align: end;
  font-size: 16px !important;
  color: #1c1d21 !important;
}

.form-heading {
  font-size: 15px;
  font-weight: 400;
  line-height: 18px;
  color: #323232;
}
