.add-inquiry-progress-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  .line {
    height: 2px;
    width: 50px;
    background-color: #e1e1e1;
    margin: 0 10px;
  }

  .progress-item {
    display: flex;
    align-items: center;
    justify-content: center;

    .icon-wrapper {
      border: 1px solid #e1e1e1;
      border-radius: 50px;
      div {
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    &.active {
      .icon-wrapper {
        background-color: #7d63ec;
        border: 1px solid #7d63ec;
      }
      .title {
        color: #7d63ec;
      }
    }
    &.done {
      .icon-wrapper {
        background-color: #008381;
        border: 1px solid #008381;
      }
      .title {
        color: #008381;
      }
    }
    &.pending {
      .title {
        color: #e1e1e1;
      }
    }
  }
}
