.inquiry-cancel-modal {
  .heading {
    font-size: 24px;
    font-weight: 600;
    color: #323232;
  }
  .inquiry-question {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
  }
  .reasons-header {
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
  }
}

.inquiry-reassign-modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-items: center;
  padding: 0px 24px 32px 24px;
  .heading {
    font-size: 24px;
    font-weight: 600;
    text-align: left;
    color: #323232;
  }

  .description {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: #4c4c4c;
  }

  .dropdown-header {
    font-size: 16px;
    font-weight: 500;
    text-align: center;
    color: #666666;
    margin-bottom: 20px;
  }

  img {
    width: 125.44px;
    height: 126px;
    margin-bottom: 20px;
  }
}
