.reminders {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.reminder-group {
  padding: 0px 10px;
}

.list-heading {
  font-size: 16px;
  font-weight: 600;
  color: #1c1d21;
}
.main-headings {
  font-size: 22px;
  font-weight: 600;
}

.reminder-badges {
  background-color: #5e81f41c;
  padding: 2px 10px;
  border-radius: 24px;
  font-size: 10px;
  color: #5e81f4;
  margin-left: 6px;
}
.reminder-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reminder-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 13px;
}
.modal-heading {
  color: #433f5c;
  font-weight: 600;
  font-size: 16px;
}

.summary-title {
  font-size: 13px;
  color: #323232;
  font-weight: 600;
}

.border-end {
  border-right: 1px dashed #dee2e6 !important;
}

.btn-check:checked + .btn,
.btn.active,
.btn.show,
.btn:first-child:active,
:not(.btn-check) + .btn:active {
  border-color: transparent !important;
}

.reminder-card {
  padding: 12px;
  border: 1px solid #e4e5eb;
  border-radius: 8px;
  margin-bottom: 16px;
}

.heading-border {
  border-top: 1px solid #e4e5eb;
}
.form-select:focus {
  border-color: #86b7fe;
  outline: 0;
  box-shadow: none !important;
}
.custom-form-select {
  border: none !important;
  border-bottom: 1px solid #dee2e6 !important;
  border-radius: 0 !important;
  padding-left: 0 !important;
}

.general-reminder-wrapper {
  height: calc(100% - 80px) !important;
  overflow-x: auto;
}

.reminder-item {
  flex: 1 0 200px;
  min-width: 200px;
}
.reminder-container {
  height: calc(100% - 45px) !important;
  // margin-bottom: 10px;
  overflow-y: auto;
}

.reminder-desc{
  overflow: hidden;
  word-wrap: break-word;
}

.reminder-container::-webkit-scrollbar {
  height: 4px;
  width: 4px;
}
.reminder-container::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 2px;
}
.reminder-container::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}
