.tax-wrapper {
  .delete-col {
    width: 70px;
  }

  .no-taxes-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  .item-input-width {
    width: 300px;
  }

  .tax-col-width {
    width: 200px;
    .tax-input-field {
      &.invalid {
        border: 1px solid #eb5e5e !important;
      }
      width: 150px;
      border: 1px solid #e5e5e5;
      border-radius: 6px;
      display: flex;
      align-items: center;
      padding: 0 10px;
      background-color: white;
      input {
        border: none;
      }
      input:focus {
        box-shadow: none;
      }
      input:disabled {
        box-shadow: none;
        background-color: white;
      }
    }
  }
}
