.guest-profile {
  .guest-info-wrapper {
    margin-bottom: 20px;
    .guest-info-label {
      font-size: 14px;
      font-weight: 400;
      line-height: 18px;
      color: #888888;
    }
    .guest-value {
      font-size: 15px;
      font-weight: 500;
      line-height: 18px;
      color: #282828;
      margin-top: 6px;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
