.payment {
  .simple-table {
    tbody {
      tr {
        height: 45px;
      }
    }
  }
  .billing-header {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    .title {
      font-size: 16px;
      font-weight: bold;
      color: #5a557a;
    }
    .line-diff-billing {
      border-bottom: 0.5px solid #e7e4ff;
      flex-grow: 1;
    }
  }
  .container-card {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    .card-title {
      div {
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
      }
    }

    .amount {
      text-align: right;
      font-size: 22px;
      font-weight: 700;
      white-space: nowrap;
    }

    .amount-word {
      text-align: right;
      font-size: 14px;
      color: #666;
      text-transform: capitalize;
    }
  }
  .blue-card {
    padding: 12px 40px 16px 32px;
    border-radius: 12px;
    border-left: 6px solid #7d63ec;
    background: rgba(125, 99, 236, 0.05);
    .amount {
      color: #7d63ec;
    }
  }

  .green-card {
    padding: 12px 40px 16px 32px;
    border-radius: 12px;
    border-left: 6px solid #13a88d;
    background: rgba(19, 168, 141, 0.05);
    .amount {
      color: #13a88d;
    }
  }

  .red-card {
    padding: 12px 40px 16px 32px;
    border-radius: 12px;
    border-left: 6px solid #ea4444;
    background: rgba(234, 68, 68, 0.05);
    .amount {
      color: #ea4444;
    }
  }

  .upload-image-container {
    position: relative;
    gap: 24px;
    display: flex;
    flex-wrap: wrap;
    .upload-media {
      position: relative;
      width: 200px;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 120px;
      border-radius: 6px;
      padding: 8px 16px;
      background-image: url("data:image/svg+xml,%3csvg width=%27100%25%27 height=%27100%25%27 xmlns=%27http://www.w3.org/2000/svg%27%3e%3crect width=%27100%25%27 height=%27100%25%27 fill=%27none%27 stroke=%27%23E4E5EBFF%27 stroke-width=%272%27 stroke-dasharray=%276%27 stroke-dashoffset=%270%27 stroke-linecap=%27square%27/%3e%3c/svg%3e");

      .input-click {
        position: absolute;
        inset: 0 0 0 0;
        opacity: 0;
        outline: none;
        z-index: 3;
        cursor: pointer;
      }
    }
    .image-ui {
      position: relative;
      overflow: hidden;
      border-radius: 8px;
      border: 1px solid #e4e5eb;
      .upload-media-image {
        position: relative;
        width: 230px;
        height: 120px;
        border-radius: 8px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .image-name-text {
        position: absolute;
        bottom: 0;
        background: white;
        left: 0;
        right: 0;
        margin: 0;
        height: 26px;
        font-size: 12px;
        padding: 4px;
        color: #666666;
      }
      .image-delete {
        &:hover {
          box-shadow: 0 6px 16px 5px rgb(0 0 0 / 13%);
          border-radius: 5px;
        }
        transition: all 0.2s;
        position: absolute;
        top: 5px;
        right: 5px;
        z-index: 7;
      }
    }
  }

  .payment-details {
    .title {
      color: #5a557a;
      font-weight: 600;
      margin-right: 10px;
      white-space: nowrap;
    }
    .line {
      width: 100%;
      height: 1px;
      background-color: #e7e4ff;
      margin-right: 10px;
    }
    .accordion {
      margin-top: 10px;
      .accordion-item {
        border: none !important;
        .accordion-button {
          padding-left: 0px !important;
          padding-right: 0px !important;
        }
        .accordion-button:not(.collapsed) {
          background-color: transparent;
        }
      }
    }
  }
}

.modal-90w {
  max-width: 90vw !important;
}

.imagePreview {
  max-width: 90%;
  max-height: 460px;
}

.success-payment-text {
  color: #5a557a;
  font-weight: bold;
  font-size: 18px;
}

.ant-modal-content {
  border-radius: 0px !important;
}
