.billingSummary {
  .billing-header {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    .title {
      font-size: 16px;
      font-weight: bold;
      color: #5a557a;
    }
    .line-diff-billing {
      border-bottom: 0.5px solid #e7e4ff;
      flex-grow: 1;
    }
  }
}
.summary-extra-charges-form {
  .summary-extra-charges-container {
    padding: 16px;
    background: #f5f4ff;
    display: flex;
    justify-content: space-between;
    gap: 10px;
    border-radius: 4px;
    .summary-extra-charges {
      display: flex;
      gap: 30px;
    }
    .label-title {
      color: #888888;
      font-size: 13px;
      margin-bottom: 3px;
    }
    .label-value {
      color: #323232;
      font-size: 15px;
      font-family: "Lato Medium";
    }
  }
  .item-container {
    display: flex;
    gap: 20px;
    .chargesItemWidth {
      min-width: 95px;
      max-width: 140px;
    }
  }
  .label-for-minimum-revenue {
    &.checked {
      color: #1c1d21;
    }
    color: #8181a5;
    font-family: "Lato Medium";
    font-size: 15px;
  }
  .form-control {
    &:focus {
      color: #282828;
      font-family: "Lato Medium";
    }
    color: #282828;
    font-family: "Lato Medium";
  }
}

.single-option {
  font-family: "Lato Medium";
  font-size: 15px;
  color: #5a557a;
  margin-bottom: 15px;
}

.simple-table-summary {
  .total-final-amount {
    td {
      background: #7d63ec;
      color: white;
    }
  }
  thead.bold-header tr th {
    font-family: "Lato Medium";
    text-transform: uppercase;
    font-size: 14px;
    color: #323232;
  }

  .slot-list-data {
    tr:last-child {
      td {
        border-bottom: 0.5px solid #7676b1;
      }
    }
  }

  thead tr {
    border-radius: 4px;
    height: 42px;
    th {
      word-spacing: 3px;
      text-transform: uppercase;
      vertical-align: middle;
      white-space: nowrap;
      border-bottom: 1px solid #d4cfff !important;
      background: #f9f9ff;
      color: #1c1d21;
      font-size: 14px;
      padding: 8px 16px;
    }
  }
  tbody tr {
    height: 36px;
    td {
      color: #525361;
      font-weight: 500;
      font-size: 14px;
      border-bottom: 1px solid #e4e5eb;
      padding: 8px 16px;
      align-content: center;
    }
  }
  .slot-row-data {
    color: #171717;
    font-size: 14px;
    font-weight: bold;
  }
  .venue-row-data {
    td {
      background-color: #fbfafd;
      font-family: "Lato Bold";
      color: #323232;
      font-style: italic;
    }
  }
  .sub-total-row-data {
    td {
      background-color: #ede7ff;
      font-family: "Lato Bold";
      color: #323232;
    }
  }
  .option-menu-col {
    width: 50px;
  }
  .col-80 {
    width: 80px;
  }
  .col-150 {
    width: 150px;
  }
  .col-200 {
    width: 200px;
  }
}
.css-remove-discount-icon {
  background: url("./../../../../../../assets/img/venue-list-delete.svg")
    no-repeat center center/cover;
  width: 25px !important;
  margin: 6px;
}
.css-remove-discount-icon {
  background: url("./../../../../../../assets/img/venue-list-delete.svg")
    no-repeat center center/cover;
  width: 25px !important;
  margin: 6px;
}
.css-edit-discount-icon {
  background: url("./../../../../../../assets/img/edit-menu-option.svg")
    no-repeat center center/cover;
  width: 25px !important;
  margin: 6px;
}
.css-add-discount-icon {
  background: url("./../../../../../../assets/img/add-menu-option.svg")
    no-repeat center center/cover;
  width: 25px !important;
  margin: 6px;
}

.billing-summary-container {
  padding: 12px 16px;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  gap: 10px;
  &.current {
    background: #fdfdfe;
    .label-title {
      opacity: 0.5;
    }
  }
  &.updated {
    background: #fbfafd;
  }
  .discount {
    display: flex;
    gap: 30px;
  }
  .label-title {
    color: #888888;
    font-size: 13px;
    margin-bottom: 3px;
  }
  .label-value {
    color: #323232;
    font-size: 15px;
    font-family: "Lato Medium";
  }
  .item-container {
    display: flex;
    gap: 20px;
    .chargesItemWidth {
      min-width: 95px;
      max-width: 140px;
    }
  }
}

.css-view-discount-icon {
  background: url("./../../../../../../assets/img/view-menu-option.svg")
    no-repeat center center/cover;
  width: 25px !important;
  margin: 6px;
}

.pending-approval {
  background: #f2ae34;
  padding: 6px 10px;
  color: #fff;
  font-family: "Lato Medium";
  font-size: 10px;
  border-radius: 24px;
}
