.package-details {
  height: calc(100% - 130px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  .select-packages-details {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;

    .content {
      .title {
        font-size: 24px;
        color: #323232;
        font-weight: 700;
      }
      .options-container {
        margin-top: 72px;
        display: flex;
        gap: 80px;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        .options-label {
          color: #5a557a;
          font-size: 18px;
          font-weight: 500;
        }
      }
      .option {
        height: 180px;
        width: 190px;
        transition: box-shadow 0.2s, opacity 0.2s;
        border-radius: 8px;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.5;
      }
      .option:hover {
        width: 190px;
        box-shadow: rgba(125, 99, 236, 0.2) 0px 4px 16px 0px;
        font-weight: 900;
        color: #7d63ec;
        opacity: 1;
      }
    }
  }

  .package-details-header-container {
    margin-bottom: 22px;
    .title-header {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 8px;
      .title {
        color: #5a557a;
        font-weight: 700;
        font-size: 16px;
      }
      .line-diff {
        border-bottom: 0.5px solid #e7e4ff;
        flex-grow: 1;
      }
    }
    .package-dropdown-list {
      border: 1px solid #ececf2;
      .option-label {
        color: #323232;
        font-size: 15px;
      }
    }
  }
  .package-details-main-container {
    margin-bottom: 22px;
    .input-container {
      padding: 0px !important;
      min-height: 33px !important;
      font-size: 14px !important;
    }
    label {
      font-size: 13px !important;
      font-weight: 400;
    }
    .dash-line-diff {
      border-bottom: 1px dashed #e4e5eb;
      min-width: 50px;
    }

    .bottom-total {
      border-bottom: 1px solid #8b6ffa;
      display: flex;
      justify-content: space-between;
      background: #ede7ff;
      padding: 10px 20px;
      border-radius: 4px;
      color: #323232;
      font-weight: bold;
      font-size: 16px;
    }

    .bottom-total-primary {
      background: #8b6ffa;
      color: white;
      display: flex;
      padding: 10px 20px;
      justify-content: space-between;
      border-radius: 4px;
      font-weight: bold;
      font-size: 16px;
    }

    .title-header-container {
      display: flex;
      align-items: center;
      gap: 8px;
      padding: 8px 0px;

      .title {
        color: #5a557a;
        font-weight: 700;
        font-size: 16px;
      }
      .line {
        border-bottom: 1px solid #e7e4ff;
        flex-grow: 1;
      }
    }
    .add-on-count {
      color: #58596b;
      font-weight: 700;
      font-size: 15px !important;
      display: flex;
      justify-content: center;
      .inc-dec-container {
        width: 80px;
        height: 36px;
        padding: 8px;
        border-radius: 4px;
        border: 1px solid #e4e5eb;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 6px;
      }
    }
  }

  .read-only {
    opacity: 0.4;
  }

  .col-width-100px {
    width: 100px;
  }
  .col-width-150px {
    width: 150px;
  }
  .col-width-180px {
    width: 180px;
  }
  .add-bottle-dd-width {
    width: 80%;
    min-width: 200px;
  }
  .brand-dd-width {
    width: 60%;
    min-width: 200px;
  }

  .simple-table {
    thead tr.summary {
      height: 42px;
    }
    tbody.bold-font tr td {
      font-weight: 600;
    }
    tbody tr.primary-background td {
      background: #7d63ec;
      color: white;
      padding: 10px 20px;
      font-weight: bold;
      font-size: 16px;
    }
    tbody tr.add-on-count-footer td {
      background: #ede7ff;
      color: #323232;
      font-weight: 800;
      font-size: 16px;
    }
    tbody tr td {
      padding: 7px !important;
      vertical-align: middle !important;
      min-height: 45px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #58596b;
    }
    thead {
      tr:first-child {
        th {
          &:first-child {
            border-radius: 4px 0px 0px 4px;
          }
          &:last-child {
            border-radius: 0px 4px 4px 0px;
          }
        }
      }
    }

    &.footer-total {
      tbody {
        tr:nth-last-child(2) td {
          border-color: transparent !important;
        }
        tr:nth-last-child(1) {
          td {
            border-color: #8b6ffa !important;
            &:first-child {
              border-radius: 4px 0px 0px 4px;
            }
            &:last-child {
              border-radius: 0px 4px 4px 0px;
            }
          }
        }
      }
    }
  }
}

.status.active-inquiry {
  background-color: #1cb161;
  border: none;
  color: white;
  border-radius: 24px;
  font-size: 12px;
  font-weight: 500;
  padding: 2px 12px;
}

.header-display-name {
  font-size: 20px;
}

.package-details-back-arrow {
  display: flex;
  align-items: center;
  margin-top: 12px;
  font-weight: 700;
  color: #433f5c;
  font-size: 18px;
}

.select-title {
  color: #282828;
  font-weight: 600;
  font-size: 15px;
}

.select-description {
  font-size: 13px;
  color: #888888;
}

.menu-card-container {
  display: flex;
  .menu-card-wrapper {
    // border: 1px solid rgba(0, 0, 0, 0.1);
    &.selected {
      border: 1px solid #7d63ec85;
    }
    margin: 10px;
    // box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
    border-radius: 8px 8px;
    margin: 10px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 8px 8px;
    height: calc(100% - 20px);
    box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);

    .menu-card {
      width: 220px;
      height: 100%;
      .menu-header {
        background: linear-gradient(270deg, #805bea 2.6%, #947eee 100%);
        border-radius: 8px 8px 0px 0px;
        display: flex;
        justify-content: space-between;
        padding: 8px 18px;
        align-items: center;
        .menu-title {
          text-transform: capitalize;
          font-weight: 900;
          color: white;
          font-size: 14px;
        }
        .menu-subtitle {
          font-weight: 300;
          color: white;
          font-size: 14px;
        }
        .menu-price {
          font-weight: lighter;
          color: white;
          font-size: 15px;
        }
      }

      .menu-body {
        border-radius: 0px 0px 8px 8px;
        // box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
        // border: 1px solid rgba(0, 0, 0, 0.1);
        height: calc(100% - 58px);
        overflow-y: auto;
        padding: 16px;
        scrollbar-width: thin;
        .menu-label {
          display: flex;
          justify-content: space-between;
          .label {
            font-weight: 600;
            font-size: 12px;
            color: #999b9f;
          }
        }

        .menu-item {
          align-items: center;
          display: flex;
          gap: 8px;
          justify-content: space-between;
          .item-name {
            font-size: 14px;
            color: #888888;
          }
          .line-bar {
            padding: 0px 7px;
            border-bottom: 1px dashed #e4e5eb;
          }
          .item-count {
            font-size: 13px;
            font-weight: bold;
          }
        }
      }
    }
  }
}

.package-summary-section {
  display: flex;
  align-items: center;
  gap: 10px;
  .summary-heading {
    color: #5a557a;
    font-size: 16px;
    font-weight: bold;
  }
  .section-line {
    border-bottom: 0.5px solid #e7e4ff;
    flex-grow: 1;
  }
}

.height-75vh {
  height: 65vh;
}

.food-item-width {
  min-width: 200px;
}

