.alert-accordion {
  .accordion-button {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
    color: #888888 !important;
    padding: 15px;
    .request-count {
      font-family: Lato;
      font-size: 15px;
      font-weight: 500;
      line-height: 18px;
      text-align: left;
      color: #8181a5;
      padding-left: 12px;
    }
  }
  .accordion {
    .accordion-item {
      border: 0px !important;
      //   border-bottom: 0.5px solid#E4E5EB !important;
      .accordion-button:not(.collapsed) {
        background-color: transparent;
      }
    }
    .active-accordion-item {
      border: 0.5px solid#E4E5EB !important;
      border-radius: 8px;

    }
  }
}

.alert-item-container {
  .inquiry-id {
    font-family: Lato;
    font-size: 15px;
    font-weight: 700;
    line-height: 20px;
    color: #1c1d21;
  }
  .alert-item {
    padding: 12px 14px 12px 14px;
    border-radius: 6px;
    border: 1px solid #e4e5eb;
    .event-name {
      font-family: Lato;
      font-size: 16px;
      font-weight: 700;
      line-height: 24px;
      color: #1c1d21;
    }
  }
}
