.profile-container {
  height: calc(100% - 96px);
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  .profile-heading {
    color: #5a557a;
    font-size: 20px;
    font-weight: 600;
    white-space: nowrap;
  }
  .user-heading {
    color: #58596b;
    font-size: 22px;
    font-weight: bold;
  }
  .profile-uploader {
    display: flex;
    justify-content: center;
    cursor: pointer;
  }
  .profile-upload {
    position: absolute;
  }
  .design-desc {
    color: #7d63ec;
    padding: 6px 14px;
    background-color: #7c63ec2a;
    width: fit-content;
    font-size: 14px;
    border-radius: 25px;
  }
  .upload-img {
    position: relative;
    bottom: 40px;
    right: 0;
    left: 110px;
  }
  .change-images {
    width: 155px;
    height: 155px;
    border-radius: 50%;
  }
  .header-seperator {
    background-color: #e4e5eb;
    width: 100%;
    height: 1px;
  }
  .image-container {
    height: 170px;
    width: 170px;
  }
  .profile-image {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .profile-data-wrapper {
    margin-bottom: 20px;
    .data-header {
      color: #888888;
      font-size: 14px;
    }
    .profile-value {
      color: #282828;
      font-size: 15px;
    }
  }
}
