.customDateTimePicker {
  &.readable {
    &.ant-picker-disabled {
      &:hover {
        background-color: #fff !important ;
        border: none !important;
      }
      background-color: #fff !important ;
      border: none !important;
      cursor: auto;
      .ant-picker-suffix {
        display: none;
      }
      input[disabled] {
        color: #282828;
        cursor: auto;
      }
    }
  }

  &.ant-picker-disabled {
    &:hover {
      background-color: #fafafa !important ;
    }
    background-color: #fafafa !important ;
  }

  &:focus-within {
    box-shadow: none !important;
    border-color: #dde1e5 !important;
  }

  &:hover {
    border-color: #dde1e5 !important;
  }

  border-color: #dee2e6;
  border-left: 0px;
  border-right: 0px;
  border-top: 0px;
  border-radius: 0px;
  width: 100%;
  padding: 3px 7px 3px;

  .ant-picker-input {
    input {
      font-size: 15px;
      color: #282828;
    }
  }
}
