.inquiry-cancelled-alert {
  padding: 15px 20px 2px;
  border-radius: 10px;
  border: 1px solid #eb5e5e;
  background-color: #fff2f2;

  .label {
    font-size: 13px;
    font-weight: 400;
    color: #888888;
  }

  .desc {
    font-size: 14px;
    font-weight: 500;
    color: #282828;
  }
}

/* booking status*/
.booking-request-pending,
.booking-request-approved,
.booking-request-rejected {
  display: flex;
  padding: 12px 18px;
  flex-direction: row;
  align-items: center;
  gap: 18px;
  border-radius: 8px;
  .label {
    font-size: 15px;
    font-weight: 400;
    line-height: 18px;
    color:#323232
  }
}

.booking-request-pending {
  border-radius: 8px;
  border: 1px solid #f2ae34;
  background: #fff7e8;
}
.booking-request-approved {
  border-radius: 8px;
  border: 1px solid #1cb161;
  background: #ebfff4;
}
.booking-request-rejected {
  border-radius: 8px;
  border: 1px solid #eb5e5e;
  background: #fff2f2;
}

/* review booking status*/

.review-booking-request-pending,
.review-booking-request-approved,
.review-booking-request-rejected {
  padding: 10px 24px;
  border-radius: 8px;
  margin: 16px 0px;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  column-gap: 40px;
  box-shadow: 0px 2px 6px 0px #0000000f;

  .data-item {
    display: flex;
    flex-direction: column;
    gap: 6px;

    .label {
      font-size: 13px;
      font-weight: 400;
      color: #888888;
    }

    .desc {
      font-size: 14px;
      font-weight: 500;
      color: #282828;
    }
  }
}

.review-booking-request-pending {
  border: 1px solid #e5960a;
  .pending {
    color: #e5960a !important;
  }
}

.review-booking-request-approved {
  border: 1px solid #1cb161;
  .approved {
    color: #1cb161 !important;
  }
}

.review-booking-request-rejected {
  border: 1px solid #eb5e5e;
  flex-direction: column;
  .top-row {
    display: flex;
    flex-direction: row;
    gap: 40px;
    margin-bottom: 6px;
    flex-wrap: wrap;
  }
  .rejected {
    color: #eb5e5e !important;
  }
}
