.VerticalStepper {
  padding: 24px 22px;
  background: #fbfbff;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
  .progress-stepper-items {
    .progress-stepper-item {
      display: flex;
      align-items: center;
      gap: 8px;
      .icon-container {
        height: 26px;
        width: 26px;
        background: #e7e4ff;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 7px;
      }
      .icon-title {
        color: #7676b1;
      }

      &.active {
        .icon-container {
          background: #745be3;
        }
        .icon-title {
          color: #745be3;
          font-weight: bold;
        }
      }
      &.done {
        .icon-container {
          background: #00aeab;
        }
        .icon-title {
          color: #282828;
        }
      }
    }

    .progress-stepper-line:not(:last-child) {
      height: 30px;
      width: 1px;
      border: 1px dotted #d8ccff;
      margin-left: 12px;
    }
  }
}
