.e-slider-tooltip.e-tooltip-wrap.e-popup {
  background-color: #745be3 !important;
}
.e-slider-tooltip.e-tooltip-wrap.e-popup .e-arrow-tip-inner {
  color: #745be3 !important;
}
.e-slider-tooltip.e-tooltip-wrap.e-popup .e-arrow-tip-outer.e-tip-top {
  border-bottom-color: #745be3 !important;
}
.e-slider-tooltip.e-tooltip-wrap.e-popup .e-arrow-tip-outer.e-tip-bottom {
  border-top-color: #745be3 !important;
}

.e-tooltip-wrap {
  border-radius: 4px;
  opacity: 1;
  gap: 10px !important;
  border-radius: 6px !important;
  // background: #ffffff !important;
  box-shadow: 0px 4px 16px 0px #18152414 !important;
  border: none;
  .e-tip-content {
    font-size: 9px !important;
  }
}

.e-control-wrapper.e-slider-container .e-slider .e-handle {
  background-color: #745be3 !important;
}

.e-control-wrapper.e-slider-container .e-slider .e-range {
  background-color: #745be3 !important;
}

.e-tooltip-wrap.e-popup {
  // background-color: #fff !important;
  border: 0px solid #000 !important;
}

.e-tooltip-wrap .e-tip-content {
  div {
    display: flex;
    justify-content: space-between;
    row-gap: 4px;
    flex-direction: column;
    :nth-child(1) {
      font-size: 12px;
      font-weight: 700;
      // color: #7676b1;
    }
    :nth-child(2) {
      font-size: 12px;
      font-weight: 400;
      // color: #58596b;
    }
  }
}

