.custom-offcanvas-btn-wrapper {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.canvas-heading {
  color: #5a557a;
}
.custom-form-label {
  color: #888888;
  font-size: 14px;
}
.custom-form-select {
  border: none !important;
  border-bottom: 1px solid #dee2e6 !important;
  border-radius: 0 !important;
  padding-left: 0 !important;
}
.custom-alerts-offcanvas {
  width: 475px !important;
}
.upper-wrapper {
  max-height: calc(100vh - 160px);
  overflow-y: auto;
  overflow-x: hidden;
}
.upper-wrapper::-webkit-scrollbar {
  display: none;
}
